<template>
    <FieldMask
        ref="field"

        v-bind="$attrs"
        inputmode="tel"
        autocomplete="tel"
        :mask="$data._mask"

        v-on="$listeners"
        @keydown="prepare"
        @validate="validate"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>

        <template #actionStart v-if="flag">
            <ElementFlag :country="flag"/>
        </template>

        <template #message>Введите {{ ruOnly ? 'российский' : '' }} номер телефона в международном формате</template>
    </FieldMask>
</template>

<script>
import { IField } from './BaseField';
import FieldMask from './FieldMask';
import ElementFlag from './ElementFlag';
import { phonePatterns } from '../js/Format';

export default {
    name: 'FieldPhone',
    components: { ElementFlag, FieldMask },
    extends: IField('field'),

    props: {
        ruOnly: Boolean,
    },

    data() {
        return {
            _mask: this.getMask(),
            flag: null,
        };
    },

    methods: {
        getPatterns() {
            const patterns = [
                ...phonePatterns,
                {
                    mask: '0',
                    startsWith: '8',
                    country: 'ru',
                    prepare: () => '+7',
                },
            ];

            return this.ruOnly ? patterns.filter((p) => p.country === 'ru') : patterns;
        },

        getMask() {
            return {
                mask: this.getPatterns(),
                dispatch: (appended, imask) => {
                    const phone = (imask.value + appended).replace(/\D/g, '');

                    this.flag = null;
                    if (!phone) return null;

                    return imask.compiledMasks.find((m) => {
                        const result = phone.indexOf(m.startsWith.substring(0, phone.length)) === 0;

                        if (result && phone) this.flag = m.country;
                        return result;
                    });
                },
            };
        },

        prepare(e) {
            const $input = e.target;

            let { value } = $input;
            value = value.substring(0, $input.selectionStart) + e.key + value.substring($input.selectionEnd);
            value = value.replace(/\D/g, '');

            const match = this.getPatterns()
                .find((p) => value.indexOf(p.startsWith.substring(0, value.length)) === 0);

            if (!match) e.preventDefault();
        },

        validate() {
            if (!this.$refs.field.validate()) return false;

            const value = this.getValue().replace(/[^\d]/g, '');
            const pattern = phonePatterns.find((p) => value.indexOf(p.startsWith) === 0);

            if (!pattern || pattern.mask.replace(/[^\d]/g, '').length !== value.length) {
                this.setError('Пожалуйста, укажите номер телефона в международном формате.');
                return false;
            }

            // if (this.max && this.getValue() > this.max) {
            //     this.setError(`Пожалуйста, укажите номер телефона в международном формате.`);
            //     return false;
            // }

            return true;
        },
    },
};
</script>
