// https://qna.habr.com/q/563475
const types = [
    {
        name: 'Visa',
        format: /^4/,
        image: require('../assets/paysystems/visa.svg'),
    },
    {
        name: 'Mastercard',
        format: /^(5[1-5]|(?:222[1-9]|22[3-9][0-9]|2[3-6][0-9]{2}|27[01][0-9]|2720))/,
        image: require('../assets/paysystems/mastercard.svg'),
    },
    {
        name: 'Maestro',
        format: /^(50|5[6-9]|6)/,
        image: require('../assets/paysystems/maestro.svg'),
    },
    {
        name: 'Мир',
        format: /^220[0-4]/,
        image: require('../assets/paysystems/mir.svg'),
    },
];

export default class BankCard {
    static getType(number) {
        return types.find((type) => type.format.test(number));
    }

    static isValid(number) {
        return !!BankCard.getType(number);
    }

    static getNames() {
        const names = types.map((type) => type.name);
        return names.join(', ');
    }
}
