<template>
    <FieldMask
        ref="field"

        v-bind="$attrs"
        :inputmode="decimal ? 'decimal' : 'numeric'"
        :mask="{
            mask: Number,
            scale: this.decimal ? 2 : 0,
            thousandsSeparator: ' ',
            padFractionalZeros: false,
            radix: ',',
            min: this.min,
            max: this.max ? '9'.repeat(this.max.toString().length) + '.99' : null,
        }"

        v-on="$listeners"
        @validate="validate"
        @blur="format"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>
    </FieldMask>
</template>

<script>
import { IField } from './BaseField';
import FieldMask from './FieldMask';

export default {
    name: 'FieldNumber',
    components: { FieldMask },
    extends: IField('field'),

    props: {
        decimal: Boolean,
        min: Number,
        max: Number,
    },

    methods: {
        format() {
            if (this.max && this.getValue() > this.max) {
                this.setValue(this.max.toString());
            }
        },

        validate() {
            if (!this.$refs.field.validate()) return false;

            if (this.max && this.getValue() > this.max) {
                this.setError(`Пожалуйста, укажите значение не более ${this.$format.number(this.max)}.`);
                return false;
            }

            return true;
        },
    },
};
</script>
