<template>
    <div class="snackbar">
        <transition-group tag="div"
                          enter-active-class="animation faster slide-from-top"
                          leave-active-class="animation faster slide-to-top"
        >
            <div
                v-for="message in messagesList"
                :key="message.id"
                class="snackbar__message"
                :class="`snackbar__message_type_${message.type}`"
            >
                <div class="snackbar__content">
                    <div class="snackbar__title">
                        {{ getMessageTitle(message.type) }}
                    </div>
                    {{ message.text }}
                    <BaseLink
                        v-if="message.action"
                        @click="removeMessage(message.id)"
                    >
                        Отменить
                    </BaseLink>
                </div>
                <div
                    v-if="message.action"
                    class="snackbar__progress"
                    :style="{ '--progress-duration': $data._processDuration }"
                >
                    <svg class="progress" width="20" height="20" viewBox="0 0 20 20">
                        <circle
                            class="snackbar__progress-line"
                            cx="10"
                            cy="10"
                            r="9.25"
                            stroke-width="1.5"
                            stroke-dasharray="58.119464091411174 58.119464091411174"
                        />
                    </svg>
                    <div class="snackbar__progress-timer">
                        {{ message.actionTimer }}
                    </div>
                </div>
            </div>
        </transition-group>
    </div>
</template>

<script>
export let snackbarInstance;

export default {
    name: 'TheSnackbar',

    data() {
        return {
            messages: new Map(),
            messagesChangeTracker: 1,

            _duration: 4,
            _processDuration: 5,
        };
    },

    computed: {
        messagesList() {
            return this.messagesChangeTracker && Array.from(this.messages.values());
        },
    },

    created() {
        snackbarInstance = this;
    },

    methods: {
        getMessageTitle(type) {
            switch (type) {
            case 'success': return 'Поздравляем!';
            case 'error': return 'Ошибка';
            case 'notify': return 'Оповещение';
            }
        },

        addMessage(type, text, action = null) {
            const id = `message-${this.$getRandomString()}`;

            this.messages.set(id, {
                id, type, text, action,
            });
            this.messagesChangeTracker += 1;

            if (action) {
                this.messages.get(id).actionTimer = this.$data._processDuration;

                const tick = () => {
                    if (!this.messages.has(id)) return;

                    this.messages.get(id).actionTimer -= 1;
                    this.messagesChangeTracker += 1;

                    if (this.messages.get(id).actionTimer !== 0) {
                        setTimeout(tick, 1000);
                    } else {
                        this.$nextTick(() => {
                            this.removeMessage(id);
                            action();
                        });
                    }
                };

                setTimeout(tick, 1000);
                return;
            }

            setTimeout(() => {
                this.removeMessage(id);
            }, this.$data._duration * 1000);
        },

        removeMessage(id) {
            this.messages.delete(id);
            this.messagesChangeTracker += 1;
        },

        success(text) {
            this.addMessage('success', text);
        },

        error(text) {
            this.addMessage('error', text);
        },

        notify(text) {
            this.addMessage('notify', text);
        },

        process(text, action) {
            this.addMessage('process', text, action);
        },
    },
};
</script>

