            left: 0;
        }

        @media screen and (min-width: 480px) {
            .modal-active & {
                right: var(--scroll-width);
            }
        }

        // Обработка элемента DOM, который вставляет компонент transition-group
        > div {
            display: flex;
            flex-flow: column;

            @media screen and (min-width: 480px) {
                align-items: flex-end;
            }
        }

        &__message {
            position: relative;
            padding: 10px 15px;
