            box-shadow: 0 7px 20px 5px var(--ui__snackbar_shadow);
            display: flex;
            align-items: center;

            &:not(:first-child) {
                margin-top: 10px;
            }

            @media screen and (min-width: 480px) {
                max-width: 350px;
            }

            &:before {
                content: '';
                position: absolute;
                top: 0;
                bottom: 0;
                left: 0;
                width: 8px;
            }

            &_type {
                &_success {
                    background: var(--ui__snackbar_success_background);
                    color: var(--ui__snackbar_success_color);

                    &:before {
                        background: var(--ui__snackbar_success_line);
                    }
                }

                &_error {
                    background: var(--ui__snackbar_error_background);
                    color: var(--ui__snackbar_error_color);

                    &:before {
                        background: var(--ui__snackbar_error_line);
                    }
                }

                &_notify {
                    background: var(--ui__snackbar_notify_background);
                    color: var(--ui__snackbar_notify_color);

                    &:before {
                        background: var(--ui__snackbar_notify_line);
                    }
                }

                &_process {
                    background: var(--ui__snackbar_process_background);
                    color: var(--ui__snackbar_process_color);
                }
            }
        }

        &__title {
            font-weight: bold;
        }

        &__content {
            flex-grow: 1;
        }

        // https://codepen.io/xgad/post/svg-radial-progress-meters
        &__progress {
            margin-left: 20px;
            position: relative;

            svg {
                display: block;
                transform: rotate(90deg) scale(-1, 1);
            }

            &-line {
                stroke: var(--white);
                fill: none;
                animation: snackbar-progress ~'calc(var(--progress-duration) * 1s)' linear;
                animation-fill-mode: both;
            }

            &-timer {
                position: absolute;
