import moment from 'moment';

moment.locale('ru');

export const phonePatterns = [
    {
        mask: '{+}000 00 000-00-00',
        startsWith: '375',
        country: 'by',
    },
    {
        mask: '{+}000 00 0000000',
        startsWith: '994',
        country: 'az',
    },
    {
        mask: '{+}0 000 000-00-00',
        startsWith: '79',
        country: 'ru',
    },
    {
        mask: '{+}0 000 000-00-00',
        startsWith: '77',
        country: 'kz',
    },
    {
        mask: '{+}000 00 000-00-00',
        startsWith: '380',
        country: 'ua',
    },
    {
        mask: '{+}000 000 00-00-00',
        startsWith: '995',
        country: 'ge',
    },
    {
        mask: '{+}000 00000000',
        startsWith: '370',
        country: 'lt',
    },
    {
        mask: '{+}000 000 00-00-00',
        startsWith: '992',
        country: 'tj',
    },
    {
        mask: '{+}000 000 00-00-00',
        startsWith: '998',
        country: 'uz',
    },
    {
        mask: '{+}000 00 00-00-00',
        startsWith: '374',
        country: 'am',
    },
    {
        mask: '{+}000 00 000 000',
        startsWith: '371',
        country: 'lv',
    },
    {
        mask: '{+}000 000 00000',
        startsWith: '373',
        country: 'md',
    },
    {
        mask: '{+}000 0000 0000',
        startsWith: '372',
        country: 'ee',
    },
    {
        mask: '{+}000 000 00-00-00',
        startsWith: '996',
        country: 'kg',
    },
];

export default class Format {
    static number(number) {
        const result = new Intl.NumberFormat('ru-RU').format(number);
        return Format.nbsp(result);
    }

    static percent(percent) {
        return `${Format.money(percent, false)}%`;
    }

    static money(amount, symbol = true) {
        amount = new Intl.NumberFormat('ru-RU', { style: 'currency', currency: 'RUB' })
            .format(amount)
            .replace(/[^0-9,.\-\s]/g, '')
            .replace(/,00/g, '')
            .trim();

        if (symbol) {
            amount = `${amount} ₽`;
        }

        return Format.nbsp(amount);
    }

    static declensionWord(num, ...words) {
        const l = words.length - 1;
        return words[(num = (num %= 100) > 19 ? (num % 10) : num) === 1 ? 0 : ((num > 1 && num <= 4) ? 1 : l)];
    }

    static datetime(time, soft = false) {
        if (Math.round(time).toString().length === 10) time *= 1000;

        time = moment(time);

        const formatted = () => {
            if (time.isSame(moment(), 'day')) {
                if (soft) return time.fromNow();
                return time.format('сегодня в HH:mm');
            }

            if (time.isSame(moment().subtract(1, 'days'), 'day')) {
                return time.format('вчера в HH:mm');
            }

            if (time.isSame(moment().add(1, 'days'), 'day')) {
                return time.format('завтра в HH:mm');
            }

            if (time.isSame(moment(), 'year')) {
                return time.format('DD MMMM в HH:mm');
            }

            return moment(time).format('DD MMMM YYYY');
        };

        // Замена пробела на неразрывной пробел
        return Format.nbsp(formatted());
    }

    static card(number) {
        const result = number.match(/\d{4}/g).join(' ');
        return Format.nbsp(result);
    }

    static phone(phone) {
        const patten = phonePatterns.find((patten) => phone.indexOf(`+${patten.startsWith}`) === 0);

        if (!patten) return phone;

        const numbers = phone.match(/\d/g);
        const result = patten.mask.replace(/[{}]/g, '').replace(/\d/g, () => numbers.shift());
        return Format.nbsp(result);
    }

    static nbsp(text) {
        const NBSP = String.fromCharCode(0xA0);
        return text.replace(/\s/g, NBSP);
    }

    static text(text) {
        text = Format.htmlSpecialChars(text);
        const urlTag = (link, text) => `<a class="link" href="${link}" target="_blank">${text}</a>`;

        return text
            .replace(/\n/g, '<br>')

            .replace(/\[b\](.*?)\[\/b\]/gi, (_, value) => `<strong>${value}</strong>`)
            .replace(/\[i\](.*?)\[\/i\]/gi, (_, value) => `<em>${value}</em>`)
            .replace(/\[u\](.*?)\[\/u\]/gi, (_, value) => `<ins>${value}</ins>`)
            .replace(/\[url=(.*?)\](.*?)\[\/url\]/gi, (_, link, text) => urlTag(link, text))

            .replace(/(https?:\/\/)?(([A-Za-zА-Яа-яЁё0-9@][A-Za-zА-Яа-яЁё0-9@\-\_\.]*[A-Za-zА-Яа-яЁё0-9@])(\/([A-Za-zА-Яа-я0-9@\-\_#%&?+\/\.=;:~]*[^\.\,;\(\)\?<\&\s:])?)?)(?![^<]*>|[^<>]*<\/a)/gi, (...args) => {
                let match = args[0];
                const protocol = args[1] || 'http://';
                const url = args[2];
                const host = args[3];

                if (host.indexOf('.') === -1 || host.indexOf('..') !== -1) {
                    return match;
                }

                const domain = host.split('.').pop();
                const domains = 'place,camera,info,name,academy,aero,arpa,coop,media,museum,mobi,travel,xxx,asia,biz,com,net,org,gov,mil,edu,int,tel,ac,ad,ae,af,ag,ai,al,am,an,ao,aq,ar,as,at,au,aw,ax,az,ba,bb,bd,be,bf,bg,bh,bi,bj,bm,bn,bo,br,bs,bt,bv,bw,by,bz,ca,cc,cd,cf,cg,ch,ci,ck,cl,cm,cn,co,cr,cu,cv,cx,cy,cz,de,dj,dk,dm,do,dz,ec,ee,eg,eh,er,es,et,eu,fi,fj,fk,fm,fo,fr,ga,gd,ge,gf,gg,gh,gi,gl,gm,gn,gp,gq,gr,gs,gt,gu,gw,gy,hk,hm,hn,hr,ht,hu,id,ie,il,im,in,io,iq,ir,is,it,je,jm,jo,jp,ke,kg,kh,ki,km,kn,kp,kr,kw,ky,kz,la,lb,lc,li,lk,lr,ls,lt,lu,lv,ly,ma,mc,md,me,mg,mh,mk,ml,mm,mn,mo,mp,mq,mr,ms,mt,mu,mv,mw,mx,my,mz,na,nc,ne,nf,ng,ni,nl,no,np,nr,nu,nz,om,pa,pe,pf,pg,ph,pk,pl,pm,pn,pr,ps,pt,pw,py,qa,re,ro,ru,rs,rw,sa,sb,sc,sd,se,sg,sh,si,sj,sk,sl,sm,sn,so,sr,st,su,sv,sy,sz,tc,td,tf,tg,th,tj,tk,tl,tm,tn,to,tp,tr,tt,tv,tw,tz,ua,ug,uk,um,us,uy,uz,va,vc,ve,vg,vi,vn,vu,wf,ws,ye,yt,yu,za,zm,zw,рф,укр,сайт,онлайн,срб,cat,pro,local';

                if (domain.length > 7 || !domains.split(',').find((d) => d == domain.toLowerCase())) {
                    return match;
                }

                if(match.indexOf('@') !== -1) {
                    return match;
                }

                try {
                    match = decodeURIComponent(match);
                } catch (e) {}

                return urlTag(protocol + url, match);
            });
    }

    static htmlSpecialChars(value = '') {
        return value
            .replace(/</g, "&lt;")
            .replace(/>/g, "&gt;")
            .replace(/"/g, "&quot;");
    }
}
