<template>
    <div class="radio">
        <div
            v-if="label"
            class="radio__label"
        >
            <BaseLabel
                :text="label"
                :error="!!error"
            />
        </div>
        <div class="radio__elements">
            <label
                v-for="option in options"
                class="radio__element"
            >
                <input
                    v-bind="$attrs"
                    type="radio"
                    class="radio__element-input sr-only"
                    :value="option.value"
                    :checked="option.checked"
                    required

                    @invalid.prevent="setError"
                    @change="onChange"
                >
                <span
                    class="radio__element-box"
                    :class="{ 'radio__element-box_error': error }"
                ></span>
                <span class="radio__element-label">
                    {{ option.text }}
                    <BaseMessage v-if="option.description">
                        {{ option.description }}
                    </BaseMessage>
                </span>
            </label>
        </div>
        <div
            v-if="$slots.message"
            v-show="!error"
            class="radio__message"
        >
            <slot name="message"/>
        </div>
        <div
            v-show="error"
            class="radio__message radio__message_error"
        >
            {{ error }}
        </div>
    </div>
</template>

<script>
export default {
    name: 'BaseRadio',
    inheritAttrs: false,

    props: {
        label: String,
        options: Array,
    },

    data() {
        return {
            error: null,
        };
    },

    methods: {
        onChange(e) {
            this.error = null;
            this.$emit('change', e.target.value);
        },

        setError() {
            this.error = 'Пожалуйста, выберите один из предложенных вариантов.';
        },
    },
};
</script>

<style scoped lang="less">
    .radio {
        &__elements {
            display: flex;
            flex-flow: column;
            align-items: flex-start;
        }

        &__element {
            cursor: pointer;
            display: flex;

            &:not(:first-child) {
                margin-top: 5px;
            }

            &-box {
                display: block;
                transition: all 100ms linear;
                position: relative;
                width: 1.2rem;
                height: $width;
                border-radius: 50%;
                background: var(--ui__input_background);
                box-shadow: 0 0 0 1px var(--ui__input_border) inset;
                flex-shrink: 0;
                margin-right: 10px;
                //transform: translateY(10%);

                &:before {
                    content: '';
                    display: block;
                    position: absolute;
                    width: 0;
                    height: 0;
                    background: var(--ui__input_background_active);
                    border-radius: 50%;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    transition: all 100ms linear;
                }

                &_error {
                    box-shadow: 0 0 0 1px var(--ui__input_border_error) inset;
                }
            }

            &-label {
                display: block;
            }

            &-input:focus + &-box {
                box-shadow: 0 0 0 1px var(--ui__input_border_focus) inset;
            }

            &-input:checked + &-box:before {
                width: 50%;
                height: 50%;
            }

            &-description {
                font-size: .8em;
                line-height: 1.3;
                color: var(--text_type_secondary);
                display: block;
            }
        }

        &__label {
            margin-bottom: .4rem;
        }

        &__message {
            font-size: .8em;
            color: var(--text_type_secondary);
            margin-top: .4rem;

            &_error {
                color: var(--text_error);
            }
        }
    }
</style>
