<template>
    <UtilPortal v-if="show_">
        <div
            ref="modal"

            class="modal modal_no-display"
            :class="`modal_type_${card ? 'card' : 'page'}`"
            tabindex="0"

            v-on="$listeners"
            @click="toggleModal(false)"
            @touchstart="toggleModal(false)"
            @keydown.tab="trapFocus"
        >
            <div
                v-show="showBackgroundLayer"
                class="modal__layer-background"
                :style="{ opacity: 1 - shiftY / 100 }"
            ></div>
            <div
                ref="layerScroll"
                class="modal__layer-scroll modal_no-display"
            >
                <div class="modal__layer-block">
                    <div
                        ref="block"
                        class="modal__block"
                        :class="{ 'modal__block_in-touch': shiftY > 0 }"

                        @touchstart.stop="touchStart"
                        @touchmove.stop="touchMove"
                        @touchend="touchEnd"
                        @touchcancel="touchEnd"
                        @click.stop="provideEvent"
                    >
                        <div
                            v-if="closeButton || title || $slots.header"
                            class="modal__block-header"
                            :class="[`modal__block-header_mode_${headerMode}`]"
                        >
                            <div
                                v-if="closeButton"
                                class="modal__block-header-close-button"
                                @click="toggleModal(false)"
                            >
                                <BaseButton
                                    mode="rounded"
                                    size="small"
                                    type="secondary"
                                >
                                    <font-awesome-icon icon="times"/>
                                </BaseButton>
                            </div>
                            <div
                                v-if="title || $slots.header"
                                class="modal__block-header-content"
                            >
                                <BaseHead
                                    v-if="title"
                                    level="2"
                                >
                                    {{ title }}
                                </BaseHead>
                                <slot name="header"/>
                            </div>
                        </div>
                        <div
                            ref="content"
                            class="modal__block-content"
                        >
                            <div :id="portalOverride.targetId"></div>
                            <div>
                                <slot/>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </UtilPortal>
</template>

<script>
import UtilPortal from './UtilPortal';

const activeModals = [];
let scrollY = null;

function rubber(offset, isCard) {
    if (offset < 0) {
        return offset;
    }

    const dimension = 72;
    const resistanceRate = isCard ? 1.2 : 0.8;
    const offsettedResistance = offset * resistanceRate;

    return offsettedResistance * dimension / (offsettedResistance + dimension);
}

function setTransformStyle(element, transform) {
    element.style.transform = transform;
    element.style.webkitTransform = transform;
}

function removeModal(id) {
    const index = activeModals.findIndex((modal) => modal.modalId == id);

    if (index != -1) {
        activeModals.splice(index, 1);
    }
}

export default {
    name: 'BaseModal',
    components: { UtilPortal },

    props: {
        show: Boolean,
        title: String,

        card: Boolean,
        closeButton: {
            type: Boolean,
            default: true,
        },

        headerMode: String,
    },

    data() {
        return {
            show_: false,
            showBackgroundLayer: true,

            shiftY: 0,
            modalId: this.$getRandomString(),
        };
    },

    computed: {
        // Перезапись портала на собственный для корректного отображения компонентов внутри модала, которые тоже используют портал
        portalOverride() {
            return {
                targetId: `modal-portal-target-${this.$getRandomString()}`,

                getScrollBlock: () => this.$refs.content,
                getSafeAreaBlock: () => this.$refs.content,
            };
        },
    },

    watch: {
        show() {
            this.toggleModal(this.show);
        },
    },

    mounted() {
        if (this.show) this.toggleModal(this.show);
    },

    beforeDestroy() {
        removeModal(this.modalId);
    },

    methods: {
        // Скрытие модала при свайпе вниз

        touchStart(e) {
            if (this._touch || !e.touches) return;

            this._touch = e.touches[0].clientY;
            this._touchStartTime = Date.now();
            this.shiftY = 0;
        },

        touchMove(e) {
            const difference = e.targetTouches[0].clientY - this._touch;

            if (this.$refs.content.scrollTop === 0 && difference > 0) {
                e.preventDefault();

                const shiftYPercent = difference / this.$refs.block.offsetHeight * 100;
                this.shiftY = rubber(shiftYPercent, this.card);

                cancelAnimationFrame(this.animationFrame);
                this.animationFrame = requestAnimationFrame(() => {
                    setTransformStyle(this.$refs.block, `translate3d(0, ${this.shiftY}%, 0)`);
                });
            }
        },

        touchEnd(e) {
            const expectShiftY = this.shiftY + this.shiftY / (Date.now() - this._touchStartTime) * 240 * 0.6;
            cancelAnimationFrame(this.animationFrame);

            if (expectShiftY > 30) {
                this.$refs.modal.style.setProperty('--shift-y', this.shiftY);
                this.$nextTick(() => this.toggleModal(false));
            } else {
                setTransformStyle(this.$refs.block, null);
                this.shiftY = 0;
            }

            this._touch = null;
        },

        toggleScroll(enabled) {
            if (!enabled) {
                if (scrollY !== null) return;

                scrollY = window.scrollY;

                document.body.classList.add('modal-active');
                document.body.style.top = `-${scrollY}px`;
                return;
            }

            document.body.classList.remove('modal-active');
            document.body.style.top = '';
            window.scrollTo(0, scrollY);

            scrollY = null;
        },

        toggleModal(show) {
            if (show == this.show_) return;

            if (show) {
                this.show_ = show;
                this.toggleScroll(false);

                if (activeModals.length > 0) {
                    this.showBackgroundLayer = false;
                    activeModals[activeModals.length - 1].animateToggle(false);
                } else {
                    this.showBackgroundLayer = true;
                }

                activeModals.push(this);

                const tick = () => {
                    if (!this.$refs.block) {
                        setTimeout(tick, 1);
                        return;
                    }

                    this.$emit('ready', this.$refs.modal);

                    this.animateToggle(show, true, () => {
                        this.$emit('update:show', true);
                        this.$emit('visible', this.$refs.modal);
                    });
                };
                tick();

                return;
            }

            // activeModals.pop();
            removeModal(this.modalId);

            if (activeModals.length > 0) {
                activeModals[activeModals.length - 1].animateToggle(true);
            }

            this.animateToggle(show, true, () => {
                this.show_ = show;

                if (activeModals.length === 0) {
                    this.toggleScroll(true);
                }

                this.shiftY = 0;
                this.$emit('update:show', false);
                this.$emit('hidden', this.$refs.modal);
            });
        },

        animateToggle(show, animateBackgroundLayer, callback) {
            const className = show ? 'show' : 'hide';

            this.$refs.modal.classList.remove('modal_no-display');
            this.$refs.layerScroll.classList.remove('modal_no-display');

            if (animateBackgroundLayer) {
                this.$animate(this.$refs.modal, `modal_animation_background-${className}`);
            }

            this.$animate(this.$refs.block, `modal_animation_block-${className}`, () => {
                if (show) {
                    this.$refs.modal.focus();
                } else {
                    this.$refs.layerScroll.classList.add('modal_no-display');
                }

                if (callback) callback();
            });
        },

        trapFocus(e) {
            const $focusableList = this.$refs.modal.querySelectorAll(
                'a[href]:not([disabled]), '
                    + 'button:not([disabled]), '
                    + 'textarea:not([disabled]), '
                    + 'input:not([type="radio"]):not([type="hidden"]):not([disabled]), '
                    + 'input[type="radio"]:checked, '
                    + 'select:not([disabled]), '
                    + '[tabindex=\'0\'], '
                    + '[contenteditable=\'true\']',
            );

            const $firstEl = $focusableList[0];
            const $lastEl = $focusableList[$focusableList.length - 1];

            if (!$firstEl) e.preventDefault();

            if (e.shiftKey) {
                if (document.activeElement === $firstEl) {
                    $lastEl.focus();
                    e.preventDefault();
                }
            } else if (document.activeElement === $lastEl) {
                $firstEl.focus();
                e.preventDefault();
            }
        },

        provideEvent() {
            // Фикс обработки popup
            document.dispatchEvent(new Event('click'));
        },
    },
};
</script>

<style lang="less">
    .modal-active {
        overflow: hidden;
        position: fixed;
    }
</style>

<style scoped lang="less">
    .scroll() {
        overflow: auto;
        overscroll-behavior: none;
        -webkit-overflow-scrolling: touch;
    }

    .modal {
        //background: var(--ui__modal_background);
        position: fixed;
        z-index: var(--level-modal);
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        // Фикс отображения состояния блока перед добавлением класса анимации
        &_no-display {
            display: none !important;
        }

        &_animation {
            &_background-show,
            &_block-show {
                animation-duration: 0.3s;
            }

            &_background-hide,
            &_block-hide {
                animation-duration: 0.1s;
                animation-direction: reverse;
            }

            &_background {
                &-show,
                &-hide {
                    animation-name: modal-opacity;
                }
            }

            &_block {
                &-show,
                &-hide {
                    animation-name: modal-fade;

                    @media screen and (min-width: 480px) {
                        animation-name: modal-zoom;
                    }
                }
            }
        }

        &_type_page & {
            &__block {
                border-radius: 15px 15px 0 0;
                padding-bottom: var(--safe-area-inset-bottom);
                transition-duration: 400ms;

                @media screen and (min-width: 480px) {
                    border-radius: 15px;
                }
            }

            &__layer-block {
                @media screen and (min-width: 480px) {
                    padding: 100px 0;
                }
            }
        }

        &_type_card & {
            &__layer-block {
                padding: 10px 10px ~'calc(10px + var(--safe-area-inset-bottom))';
            }

            &__block {
                transition-duration: 340ms;
            }
        }

        &__layer {
            &-background,
            &-scroll {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                bottom: 0;
            }

            &-background {
                background: var(--ui__modal_background);
                transition: transform 120ms cubic-bezier(.36, .66, .04, 1);

                // Подрубаем GPU в айфонах
                -webkit-transform: translate3d(0, 0, 0);
            }

            &-scroll {
                display: flex;
                .scroll();
            }

            &-block {
                margin: auto auto 0 auto;
                position: relative;
                max-width: 512px;
                width: 100%;

                @media screen and (min-width: 480px) {
                    margin: auto;
                }
            }
        }

        &__block {
            @border-radius: 15px;

            background: var(--ui__modal_block_background);
            position: relative;
            border-radius: @border-radius;
            transition: transform cubic-bezier(.36, .66, .04, 1);
            box-shadow:
                0 9px 46px 8px var(--ui__modal_block_shadow_1),
                0 11px 15px -7px var(--ui__modal_block_shadow_2),
                0 24px 38px 3px var(--ui__modal_block_shadow_3);

            @media screen and (max-width: 480px) {
                max-height: ~'calc(var(--vh) * 100 - 60px)';
                display: flex;
                flex-flow: column;
            }

            &_in-touch {
                transition: none;
            }

            &-header {
                padding: var(--content-padding);
                flex-shrink: 0;
                border-radius: @border-radius @border-radius 0 0;

                /deep/ .head {
                    margin: 0;

                    h2 {
                        line-height: 1;
                    }

                    &__description {
                        margin-top: 3px;
                    }
                }

                &-close-button {
                    float: right;
                    margin-left: 10px;
                }

                &-content {

                }

                &_mode {
                    &_gray {
                        background: var(--background_type_secondary);
                    }
                }
            }

            &-content {
                padding: var(--content-padding) var(--content-padding) 0;
                position: relative;

                // фикс бага с пропаданием нижнего паддинга при изменение контента в скролл-элементе
                &:after {
                    content: '';
                    display: block;
                    height: var(--content-padding);
                }

                @media screen and (min-width: 480px) {
                    max-height: none;
                }

                .scroll();

                @media screen and (min-width: 480px) {
                    overflow: visible;
                }
            }
        }
    }

    @keyframes modal-zoom {
        from {
            transform: scale3d(0.9, 0.9, 0.9);
        }

        to {
            transform: scale3d(1, 1, 1);
        }
    }

    @keyframes modal-fade {
        from {
            transform: translate3d(0, 100%, 0);
        }

        to {
            transform: translate3d(0, ~'calc(var(--shift-y) * 1%)', 0);
        }
    }

    @keyframes modal-opacity {
        from {
            opacity: 0;
        }

        to {
            opacity: 1;
        }
    }
</style>
