<template>
    <UtilField
        :id="id"
        :size="size"
        :appearance="appearance"
        :focus.sync="focus"
        :error="error"
        :disabled="disabled"
        class="field-textarea"
    >
        <div
            ref="fieldTextarea"
            class="textarea"
            :class="{ 'textarea_emoji-enabled': emojiEnabled }"
            @mousedown="preventBlur"
        >
            <div
                v-if="emojiEnabled"
                class="textarea__emoji"
            >
                <PopupEmoji @selectEmoji="insertEmoji">
                    <div class="textarea__emoji-button">
                        <font-awesome-icon :icon="['far', 'smile']"/>
                    </div>
                </PopupEmoji>
            </div>

            <UtilScrollbar class="textarea__scroll">
                <input
                    type="hidden"
                    :name="name"
                    :value="clearValue"
                >
                <span
                    ref="textarea"

                    v-html="$format.text(value_) + '<br>'"
                    v-bind="$attrs"
                    :id="id"
                    :contenteditable="!disabled"
                    class="textarea__input"
                    :class="{ textarea__input_formatting: formatting }"

                    v-on="$listeners"
                    @focus="focus = true"
                    @blur="focus = false"
                    @input="onInput"
                    @keydown="onKeydown"
                    @paste.prevent="onPaste"
                    @mousedown="stopPrevent"
                ></span>
            </UtilScrollbar>
            <div
                v-show="empty"
                class="textarea__placeholder"
            >
                {{ placeholder }}
            </div>

            <div
                v-if="maxlength || formatting || $slots.actionEnd"
                class="textarea__footer"
