                        <div
                            class="textarea__footer-action"
                            @click="setFormatting('bold')"
                        >
                            <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="bold" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 384 512" class="svg-inline--fa fa-smile fa-w-16">
                                <path fill="currentColor" d="M314.52 238.78A119.76 119.76 0 0 0 232 32H48a16 16 0 0 0-16 16v16a16 16 0 0 0 16 16h16v352H48a16 16 0 0 0-16 16v16a16 16 0 0 0 16 16h208a128 128 0 0 0 128-128c0-49.49-28.38-91.92-69.48-113.22zM128 80h88a72 72 0 0 1 0 144h-88zm112 352H128V272h112a80 80 0 0 1 0 160z" class=""></path>
                            </svg>
                        </div>
                        <div
                            class="textarea__footer-action"
                            @click="setFormatting('italic')"
                        >
                            <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="italic" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 320 512" class="svg-inline--fa fa-smile fa-w-16">
                                <path fill="currentColor" d="M320 48v16a16 16 0 0 1-16 16h-67l-88 352h59a16 16 0 0 1 16 16v16a16 16 0 0 1-16 16H16a16 16 0 0 1-16-16v-16a16 16 0 0 1 16-16h67l88-352h-59a16 16 0 0 1-16-16V48a16 16 0 0 1 16-16h192a16 16 0 0 1 16 16z" class=""></path>
                            </svg>
                        </div>
                        <div
                            class="textarea__footer-action"
                            @click="setFormatting('underline')"
                        >
                            <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="underline" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 448 512" class="svg-inline--fa fa-smile fa-w-16">
                                <path fill="currentColor" d="M32 48h32v208c0 88.22 71.78 160 160 160s160-71.78 160-160V48h32a16 16 0 0 0 16-16V16a16 16 0 0 0-16-16H288a16 16 0 0 0-16 16v16a16 16 0 0 0 16 16h32v208a96 96 0 0 1-192 0V48h32a16 16 0 0 0 16-16V16a16 16 0 0 0-16-16H32a16 16 0 0 0-16 16v16a16 16 0 0 0 16 16zm400 416H16a16 16 0 0 0-16 16v16a16 16 0 0 0 16 16h416a16 16 0 0 0 16-16v-16a16 16 0 0 0-16-16z" class=""></path>
                            </svg>
                        </div>
                        <div
                            class="textarea__footer-action"
                            @click="showLinkModal = true"
                        >
                            <svg aria-hidden="true" focusable="false" data-prefix="far" data-icon="link" role="img" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512" class="svg-inline--fa fa-link fa-w-16">
                                <path fill="currentColor" d="M314.222 197.78c51.091 51.091 54.377 132.287 9.75 187.16-6.242 7.73-2.784 3.865-84.94 86.02-54.696 54.696-143.266 54.745-197.99 0-54.711-54.69-54.734-143.255 0-197.99 32.773-32.773 51.835-51.899 63.409-63.457 7.463-7.452 20.331-2.354 20.486 8.192a173.31 173.31 0 0 0 4.746 37.828c.966 4.029-.272 8.269-3.202 11.198L80.632 312.57c-32.755 32.775-32.887 85.892 0 118.8 32.775 32.755 85.892 32.887 118.8 0l75.19-75.2c32.718-32.725 32.777-86.013 0-118.79a83.722 83.722 0 0 0-22.814-16.229c-4.623-2.233-7.182-7.25-6.561-12.346 1.356-11.122 6.296-21.885 14.815-30.405l4.375-4.375c3.625-3.626 9.177-4.594 13.76-2.294 12.999 6.524 25.187 15.211 36.025 26.049zM470.958 41.04c-54.724-54.745-143.294-54.696-197.99 0-82.156 82.156-78.698 78.29-84.94 86.02-44.627 54.873-41.341 136.069 9.75 187.16 10.838 10.838 23.026 19.525 36.025 26.049 4.582 2.3 10.134 1.331 13.76-2.294l4.375-4.375c8.52-8.519 13.459-19.283 14.815-30.405.621-5.096-1.938-10.113-6.561-12.346a83.706 83.706 0 0 1-22.814-16.229c-32.777-32.777-32.718-86.065 0-118.79l75.19-75.2c32.908-32.887 86.025-32.755 118.8 0 32.887 32.908 32.755 86.025 0 118.8l-45.848 45.84c-2.93 2.929-4.168 7.169-3.202 11.198a173.31 173.31 0 0 1 4.746 37.828c.155 10.546 13.023 15.644 20.486 8.192 11.574-11.558 30.636-30.684 63.409-63.457 54.733-54.735 54.71-143.3-.001-197.991z" class=""></path>
                            </svg>
                        </div>
                    </template>
                    <slot name="actionEnd"/>
                </div>
                <div
                    v-if="maxlength"
                    class="textarea__footer-maxlength"
                >
                    {{ maxlength - $getCharacterLength(value_) }}
                </div>
            </div>
        </div>

        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>
        <template #actionEnd/>

        <template #label>
            <slot name="label"/>
            <template v-if="label">
                {{ label }}
            </template>
        </template>

        <template #service>
            <BaseModal
                :show.sync="showLinkModal"
                title="Добавить ссылку"

                @ready="linkModalReady"
            >
                <BaseForm @submit="linkModalSubmit">
                    <BaseFormField
                        ref="linkModalFieldText"
                        label="Текст"
                        placeholder="Павел Дуров"
                        name="text"
                        required
                    />
                    <BaseFormField
                        type="url"
                        label="Ссылка"
                        placeholder="https://vk.com/durov"
                        name="link"
                        required
                    />
                    <BaseFormButton label="Добавить"/>
                </BaseForm>
            </BaseModal>
        </template>

<!--        <template #message>-->
<!--            !<div v-html="$format.text(value_)"/>!-->

<!--            <br><br>-->

<!--            {{ value_ }}-->

<!--        </template>-->
    </UtilField>
</template>

<script>
import UtilField from './UtilField';
import UtilScrollbar from './UtilScrollbar';

export default {
    name: 'FieldTextarea',
    components: {
        PopupEmoji: () => import(/* webpackChunkName: "PopupEmoji" */ './PopupEmoji'),
        FieldURL: () => import(/* webpackChunkName: "FieldURL" */ './FieldURL'),
        UtilField,
        UtilScrollbar,
    },

    props: {
        size: String,
        appearance: String,
        disabled: Boolean,

        label: String,
        placeholder: String,
        name: String,
        value: {
            type: String,
            default: '',
        },

        required: Boolean,
        minlength: Number,
        maxlength: Number,
        formatting: Boolean,
        emoji: Boolean,
    },

    data() {
        return {
            focus: false,
            error: null,
            value_: this.value,

            showLinkModal: false,
        };
    },

    computed: {
        id() {
            return `textarea-${this.$getRandomString()}`;
        },

        empty() {
            return !this.$getCharacterLength(this.value_);
        },

        clearValue() {
            return this.value_.trim();
        },

        emojiEnabled() {
            return this.emoji && this.$device.isDesktop();
        },
    },

    watch: {
        value() {
            this.value_ = this.value;
        },

        value_() {
            this.clearError();
        },

        focus() {
            if (this.focus) this.focusTextarea();
            else this.validate();
        },
    },

    methods: {
        onInput() {
            this.value_ = this.$refs.textarea.innerHTML
                .replace(/<br>$/, '')
                .replace(/<br>/g, '\n')
                .replace(/&nbsp;/g, ' ')

                .replace(/<(?:b|strong).*>(.*?)<\/(?:b|strong)>/gi, '[b]$1[/b]')
                .replace(/<i.*>(.*?)<\/i>/gi, '[i]$1[/i]')
                .replace(/<u.*>(.*?)<\/u>/gi, '[u]$1[/u]')
                .replace(/<a.*href="(.*?)".*>(.*?)<\/a>/gi, '[url=$1]$2[/url]');

            this.$emit('update:value', this.clearValue);
        },

        onKeydown(e) {
            this.fixSelection();

            if (e.metaKey || e.shiftKey && e.key != 'Enter') return;

            const selection = document.getSelection();

            if (!selection.isCollapsed
                && this.$refs.textarea.contains(selection.anchorNode)
                && this.$refs.textarea.contains(selection.focusNode)) return;

            const length = this.$getCharacterLength(this.value_);
            if (this.maxlength && length >= this.maxlength && (e.key == 'Enter' || this.$getCharacterLength(e.key) == 1)) e.preventDefault();
        },

        onPaste(e) {
            this.pasteText(e.clipboardData.getData('text/plain'));
        },

        insertEmoji(e) {
            this.pasteText(e.target.innerText);
        },

        focusTextarea() {
            const $el = this.$refs.textarea;

            if (document.activeElement == $el) return;
            $el.focus();

            const selection = document.getSelection();
            selection.selectAllChildren($el);
            selection.collapseToEnd();
        },

        clearError() {
            this.error = null;
        },

        setError(message) {
            this.error = message;
        },

        validate() {
            if (this.required && !this.clearValue) {
                this.setError('Пожалуйста, заполните поле.');

                return false;
            }

            if (this.minlength && this.$getCharacterLength(this.clearValue) < this.minlength) {
                const word = this.$format.declensionWord(this.minlength, 'символа', 'символов');
                this.setError(`Пожалуйста, укажите не менее ${this.minlength} ${word}.`);

                return false;
            }

            return true;
        },

        setFormatting(command) {
            document.execCommand(command, false, null);
        },

        pasteText(text) {
            this.fixSelection();

            if (this.maxlength) {
                const rangeLength = this.$getCharacterLength(document.getSelection().toString().trim());
                const availableLength = this.maxlength + rangeLength - this.$getCharacterLength(this.value_);

                text = [...text].slice(0, availableLength).join('');
            }

            document.execCommand('insertText', false, text);
        },

        fixSelection() {
            const range = document.getSelection().getRangeAt(0);

            const $el = this.$refs.textarea;
            const $brs = $el.querySelectorAll('br');
            const $lastBr = $brs[$brs.length - 1];

            const brRange = new Range();
            brRange.selectNode($lastBr);

            if (range.compareBoundaryPoints(Range.END_TO_START, brRange) != -1) {
                range.setStartBefore($lastBr);
            }

            if (range.compareBoundaryPoints(Range.START_TO_END, brRange) != -1) {
                range.setEndBefore($lastBr);
            }
        },

        linkModalReady() {
            this.fixSelection();
            const selection = document.getSelection();

            this.range = selection.getRangeAt(0).cloneRange();
            this.$refs.linkModalFieldText.setValue(selection.toString());
        },

        linkModalSubmit(formData, finishLoading) {
            this.showLinkModal = false;

            this.range.deleteContents();
            this.range.insertNode(document.createTextNode(formData.get('text')));

            const selection = document.getSelection();
            selection.removeAllRanges();
            selection.addRange(this.range);

            document.execCommand('createLink', false, formData.get('link'));
            selection.collapseToEnd();
        },

        stopPrevent(e) {
            if (this.focus) e.stopPropagation();
        },

        preventBlur(e) {
            const $el = this.$refs.fieldTextarea;
            if (this.focus && ($el == e.target || $el.contains(e.target))) e.preventDefault();
        },
    },
};
</script>

