            padding: var(--padding-y) var(--padding-x);

            &-maxlength {
                color: var(--text_type_tertiary);
                margin-left: auto;
            }

            &-actions {
                display: flex;

                ::v-deep > * {
                    &:not(:first-child) {
                        margin-left: 5px;
                    }
                }
            }

            &-action {
                width: 1.8rem;
                height: $width;

                line-height: $width;
                border-radius: 3px;
                cursor: pointer;
                display: flex;
                justify-content: center;
                align-items: center;

                .element-animation();
                .button-tertiary-interaction();

                color: var(--text_type_secondary);

                svg {
                    display: block;
                }
            }
        }
    }
</style>
