<template>
    <BaseField
        ref="field"
        v-bind="$attrs"
        inputmode="email"

        v-on="$listeners"
        @validate="validate"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>
    </BaseField>
</template>

<script>
import { IField } from './BaseField';

export default {
    name: 'FieldEmail',
    extends: IField('field'),

    methods: {
        validate() {
            if (!this.$refs.field.validate()) return false;

            const value = this.getValue();

            if (value && !/^.+@.+\..+$/.test(value)) {
                this.setError('Пожалуйста, введите адрес электронной почты.');
                return false;
            }

            return true;
        },
    },
};
</script>
