<template functional>
    <div
        :ref="data.ref"

        class="scrollbar"
        :class="[data.class, data.staticClass]"
        :style="{ 'max-height': props.maxHeight ? `${props.maxHeight}px` : 'none' }"

        @scroll="$options.emit(listeners.scroll, $event)"
        @touchstart.stop
        @touchmove.stop
    >
        <div class="scrollbar__content">
            <slot/>
        </div>
    </div>
</template>

<script>
export default {
    name: 'UtilScrollbar',

    props: {
        maxHeight: [String, Number],
    },

    emit(event, value) {
        if (event) event(value);
    },
};
</script>

<style lang="less">
    .scrollbar {
        overflow-x: hidden;
        overflow-y: auto;
        overscroll-behavior: none;
        //scroll-behavior: smooth;
        scrollbar-width: thin;
        scrollbar-color: var(--ui__scrollbar-background) transparent;
        -webkit-overflow-scrolling: touch;
        -ms-overflow-style: -ms-autohiding-scrollbar;

        &::-webkit-scrollbar {
            width: 10px;
            height: 18px;
        }

        &::-webkit-scrollbar-thumb {
            height: 6px;
            border: 2px solid transparent;
            background-clip: padding-box;
            -webkit-border-radius: 7px;
            background-color: var(--ui__scrollbar-background);
            transition: all 100ms ease;

            &:hover {
                background-color: var(--ui__scrollbar-background-hover);
            }
        }

        &::-webkit-scrollbar-button {
            width: 0;
            height: 0;
            display: none;
        }

        &::-webkit-scrollbar-corner {
            background-color: transparent;
        }

        // Фикс скролла в Firefox
        display: flex;

        &__content {
            flex-grow: 1;
            width: 100%;
        }
    }
</style>
