<template>
    <UtilField
        :id="id"
        :size="size"
        :appearance="appearance"
        :focus.sync="focus"
        :error="error"
        :disabled="disabled"
    >
        <div class="input">
            <div class="input__prefix">{{ prefix }}</div>
            <input
                ref="input"

                v-bind="$attrs"
                :id="id"
                :disabled="disabled"
                :required="xRequired"
                size="1"
                :type="$attrs.type || 'text'"

                v-on="$listeners"
                @focus="focus = true"
                @blur="focus = false"
                @input="onInput"
            >
            <div class="input__suffix">{{ suffix }}</div>
        </div>

        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>

        <template #label>
            <slot name="label"/>
            <template v-if="label">
                {{ label }}
            </template>
        </template>
    </UtilField>
</template>

<script>
import UtilField from './UtilField';

export function IField(ref) {
    return {
        methods: {
            getValue() {
                return this.$refs[ref].getValue();
            },
            setValue(value) {
                this.$refs[ref].setValue(value);
            },
            clearError() {
                this.$refs[ref].clearError();
            },
            setError(message) {
                this.$refs[ref].setError(message);
            },
            hasFocus() {
                return this.$refs[ref].focus;
            },
            validate(value = null) {
                return this.$refs[ref].validate(value);
            },
        },
    };
}

export default {
    name: 'BaseField',
    components: { UtilField },
    inheritAttrs: false,

    props: {
        size: String,
        appearance: String,

        disabled: Boolean,
        autofocus: Boolean,
        required: Boolean,
        minlength: [Number, String],

        prefix: String,
        value: [String, Number],
        suffix: String,
        label: String,

        xRequired: Boolean,
    },

    data() {
        return {
            focus: false,
            error: null,
            value_: (this.value || '').toString(),
        };
    },

    computed: {
        id() {
            return `field-${this.$getRandomString()}`;
        },
    },

    watch: {
        value() {
            this.setValue(this.value.toString());
        },

        value_() {
            this.clearError();
        },

        focus() {
            if (this.focus) {
                this.$refs.input.focus();
                return;
            }

            if (this.validate()) this.$emit('validate');
        },
    },

    mounted() {
        this.$refs.input.value = this.value_;

        if (this.autofocus) this.$refs.input.focus();
    },

    methods: {
        onInput(e) {
            this.setValue(e.target.value, false);
        },

        getValue() {
            return this.value_;
        },

        setValue(value, updateInput = true) {
            if (this.value_ == value) return;

            this.value_ = value;

            if (updateInput) {
                this.$refs.input.value = value;
            }

            this.$emit('update:value', this.value_);
        },

        clearError() {
            this.error = null;
        },

        setError(message) {
            this.error = message;
        },

        hasFocus() {
            return this.focus;
        },

        validate(value = this.getValue()) {
            switch (true) {
            case this.required && !value:
                this.setError('Пожалуйста, заполните поле.');
                return false;

            case this.minlength && value.length < this.minlength:
                const word = this.$format.declensionWord(this.minlength, 'символа', 'символов');

                this.setError(`Пожалуйста, укажите не менее ${this.minlength} ${word}.`);
                return false;
            }

            return true;
        },
    },
};
</script>

<style scoped lang="less">
    .input {
        display: flex;
        align-items: center;
        flex-grow: 1;

        &__prefix,
        &__suffix {
            pointer-events: none;
            user-select: none;
            white-space: nowrap;
        }

        &__prefix,
        &__suffix,
        input{
            height: 1.5em;
            line-height: 1.5em;
        }

        input {
            width: 100%;
            color: inherit;
            padding: 0;
            margin: 0;
            border: none;
            background: none;
            -webkit-appearance: none;
            -webkit-border-radius: 0;

            &:focus {
                outline: none;
            }

            &:invalid,
            &:-moz-ui-invalid {
                border: 0;
                outline: none;
                box-shadow: none;
            }

            &:-webkit-autofill {
                &,
                &:hover,
                &:focus,
                &:active {
                    transition: background-color 9999s ease-in-out 0s;
                }
            }

            &::placeholder {
                opacity: 1;
                color: var(--text_type_tertiary);
                overflow: hidden;
                text-overflow: ellipsis;
            }
        }
    }
</style>
