<template>
    <BaseField
        ref="field"
        v-bind="$attrs"
        inputmode="url"

        v-on="$listeners"
        @keydown.enter="format"
        @blur="format"
        @validate="validate"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>
    </BaseField>
</template>

<script>
import { IField } from './BaseField';

export default {
    name: 'FieldURL',
    extends: IField('field'),

    methods: {
        format() {
            const value = this.getValue();

            if (value && !this.validate()) {
                this.setValue(`http://${value}`);
            }
        },

        validate() {
            if (!this.$refs.field.validate()) return false;

            const value = this.getValue();

            if (value && !/^https?:\/\/.*/.test(value)) {
                return false;
            }

            return true;
        },
    },
};
</script>
