<template>
    <BaseModal
        ref="modal"

        :show="show"
        :close-button="false"

        @update:show="$emit('update:show',  $event)"
    >
        <template #header>
            <div class="calendar-header">
                <BaseButton
                    mode="rounded"
                    type="secondary"
                    size="medium"
                    @click="changeCurrentMonth(-1)"
                >
                    <font-awesome-icon icon="chevron-left"/>
                </BaseButton>
                <BaseHead
                    level="2"
                    align="center"
                    class="calendar-header__title"
                >
                    {{ getMonth() }} {{ getYear() }}
                </BaseHead>
                <BaseButton
                    mode="rounded"
                    type="secondary"
                    size="medium"
                    @click="changeCurrentMonth(1)"
                >
                    <font-awesome-icon icon="chevron-right"/>
                </BaseButton>
            </div>
        </template>

        <div class="calendar">
            <div class="calendar-main">
                <transition
                    mode="out-in"
                    :enter-active-class="'animation faster slide-from-' + (currentMonthHistory === 1 ? 'right' : 'left')"
                    :leave-active-class="'animation faster slide-to-' + (currentMonthHistory === 1 ? 'left' : 'right')"
                >
                    <div
                        class="calendar-main__days"
                        :key="currentMonth"
                    >
                        <div
                            v-for="dayOfWeek in daysOfWeek"
                            class="calendar-main__day calendar-main__day_type_name"
                        >
                            {{ dayOfWeek }}
                        </div>
                        <div
                            v-for="day in getDays()"
                            class="calendar-main__day"
                            :class="[
                                day.class,
                                {
                                    'calendar-main__day_type_in-range':
                                        day.date.isSameOrAfter(clear.rangeStart, 'day') &&
                                        day.date.isSameOrBefore(clear.rangeEnd, 'day'),

                                    'calendar-main__day_type_in-range-first': day.date.isSame(clear.rangeStart, 'day'),
                                    'calendar-main__day_type_in-range-last': day.date.isSame(clear.rangeEnd, 'day')
                                },
                            ]"
                            @click="selectRange(day.date)"
                            @mouseover="changeTarget(day.date)"
                        >
                            <div class="calendar-main__interaction">
                                {{ day.date.date() }}
                            </div>
                        </div>
                    </div>
                </transition>
            </div>
            <div class="calendar-footer">
                <BaseForm @submit="onSubmit">
                    <div
                        class="form__row calendar-footer__field"
                        :class="{ 'calendar-footer__field_range': range }"
                    >
                        <BaseField
                            name="range_start"
                            :type="timepicker ? 'time' : null"
                            :value.sync="rangeStartTime"
                            :label="range ? 'Начальная дата' : 'Дата'"
                            :prefix="clear.rangeStart.format(this.prefixFormat)"
                            :readonly="!timepicker"
                            x-required
                        />
                        <BaseField
                            v-if="range"

                            name="range_end"
                            :type="timepicker ? 'time' : null"
                            :value.sync="rangeEndTime"
                            label="Конечная дата"
                            :prefix="clear.rangeEnd.format(this.prefixFormat)"
                            :readonly="!timepicker"
                            x-required
                        />
                    </div>
                    <div class="form__actions">
                        <BaseFormButton
                            label="Выбрать"
                        />
                        <BaseButton
                            label="Отменить"
                            type="tertiary"
                            @click="$emit('update:show',  false)"
                        />
                    </div>
                </BaseForm>
            </div>
        </div>
    </BaseModal>
</template>

<script>
export default {
    name: 'ModalDatepicker',

    props: {
        show: Boolean,
        start: [Object, String, Number],
        end: [Object, String, Number],

        min: {
            type: Object,
            default() {
                return this.$moment().subtract(1, 'years');
            },
        },

        max: {
            type: Object,
            default() {
                return this.$moment().add(1, 'years');
            },
        },

        range: Boolean,
        timepicker: Boolean,
    },

    data() {
        return {
            currentMonth: 0,
            currentMonthHistory: null,

            rangeStart: null,
            rangeStartTime: null,
            rangeEnd: null,
            rangeEndTime: null,

            targetDay: null,
        };
    },

    computed: {
        clear() {
            return {
                rangeStart: this.$moment.min(this.rangeStart, this.targetDay || this.rangeStart),
                rangeEnd: this.$moment.max(this.rangeStart, this.targetDay || this.rangeEnd),
            };
        },

        daysOfWeek() {
            return this.$moment.weekdaysMin(true);
        },

        prefixFormat() {
            return `D MMMM YYYY${this.timepicker ? ` в ${String.fromCharCode(0x0200B)}` : ''}`;
        },
    },

    watch: {
        show() {
            this.currentMonth = 0;

            if (!this.show) return;
            this.initData();
        },
    },

    created() {
        this.initData();
    },

    methods: {
        getInitValue(value, isEnd = false) {
            switch (true) {
            case !value:
                return isEnd ? this.rangeStart.clone().add(1, 'milliseconds') : this.$moment();

            case this.$moment.isMoment(value):
                return value;

            case /\d{10}(\.\d{1,3})?/.test(value.toString()):
                return this.$moment.unix(value);
            }

            return this.$moment(value);
        },

        initData() {
            this.rangeStart = this.getInitValue(this.start);
            this.rangeEnd = this.getInitValue(this.end, true);

            if (this.timepicker) {
                this.rangeStartTime = this.rangeStart.format('HH:mm');
                this.rangeEndTime = this.rangeEnd.format('HH:mm');
            }
        },

        getDate() {
            return this.getInitValue(this.start).clone()
                .add(this.currentMonth, 'month')
                .date(1);
        },

        getYear() {
            return this.getDate().year();
        },

        getMonth() {
            const date = this.getDate();
            const month = this.$moment.months(date.month());

            return month.charAt(0).toUpperCase() + month.slice(1);
        },

        getDays() {
            const date = this.getDate();

            const days = [];
            for (let day = 1; day <= date.daysInMonth(); day++) {
                const _date = date.clone().date(day);
                days.push({
                    date: _date,
                    class: [
                        ...(day == 1 ? [`calendar-main__day_start-from-${_date.weekday() + 1}`] : []),
                        ...(
                            this.min && this.min.isSameOrAfter(_date, 'day')
                            || this.max && _date.isAfter(this.max, 'day')
                                ? ['calendar-main__day_disabled'] : []
                        ),
                    ],
                });
            }

            return days;
        },

        selectRange(date) {
            if (this.range && !this.targetDay) {
                this.targetDay = date;
                this.rangeStart = date;

                return;
            }

            const list = [
                ...(this.range ? [this.rangeStart] : []),
                date,
            ];

            this.rangeStart = this.$moment.min(...list).clone();
            this.rangeEnd = this.$moment.max(...list).clone();

            this.targetDay = null;
        },

        changeTarget(date) {
            if (!this.targetDay) return;
            this.targetDay = date;
        },

        changeCurrentMonth(value) {
            this.currentMonthHistory = value;
            this.currentMonth += value;
        },

        onSubmit(formData, finishLoading) {
            if (this.targetDay) this.selectRange(this.targetDay);
            finishLoading();

            const rangeStartTime = this.$moment(this.rangeStartTime, 'HH:mm');
            const rangeEndTime = this.$moment(this.rangeEndTime, 'HH:mm');

            if (rangeStartTime.isValid()) {
                this.rangeStart.hours(rangeStartTime.hours()).minutes(rangeStartTime.minutes());
            }

            if (rangeEndTime.isValid()) {
                this.rangeEnd.hours(rangeEndTime.hours()).minutes(rangeEndTime.minutes());
            }

            if (this.rangeStart.isAfter(this.rangeEnd)) {
                const start = this.rangeStart;
                this.rangeStart = this.rangeEnd;
                this.rangeEnd = start;
            }

            this.$emit('update:show', false);
            this.$emit('select', {
                rangeStart: this.rangeStart.clone(),
                rangeEnd: this.range ? this.rangeEnd.clone() : null,
            });
        },
    },
};
</script>

<style scoped lang="less">
    .calendar {

        &-header {
            display: flex;
            align-items: center;

            /deep/ &__title {
                flex-grow: 1;
                margin: 0;
            }
        }

        &-main {
            &__days {
                display: grid;
                grid-template-columns: repeat(7, 1fr);
                grid-row-gap: 3px;
            }

            &__interaction {
                cursor: pointer;
                width: 2.5rem;
                height: $width;
                line-height: $height;
                margin: 0 auto;
                border-radius: 50%;

                @media screen and (min-width: 480px) {
                    &:hover {
                        background: var(--ui__button_primary_background);
                        color: var(--ui__button_primary_color);
                    }
                }
            }

            &__day_type_in-range-first &,
            &__day_type_in-range-last & {
                &__interaction {
                    background: var(--ui__button_primary_background);
                    color: var(--ui__button_primary_color);
                }
            }

            &__day {
                height: 2.5rem;
                line-height: $height;
                text-align: center;

                &_disabled {
                    color: var(--text_type_tertiary);
                    pointer-events: none;
                }

                &_type_name {
                    color: var(--text_type_secondary);
                    text-transform: uppercase;
                    font-size: .8em;
                }

                &_type_in-range {
                    position: relative;
                    transform-style: preserve-3d;

                    &:before {
                        content: '';
                        display: block;
                        position: absolute;
                        width: 100%;
                        height: 100%;
                        background: var(--ui__datepicker_range_background);
                        transform: translateZ(-1px);
                    }

                    &-first,
                    &-last {
                        &:before {
                            //width: 50%;
                            // Фикс полоски в firefox
                            width: 51%;
                        }
                    }

                    &-first {
                        &:before {
                            right: 0;
                        }
                    }

                    &-first&-last {
                        &:before {
                            display: none;
                        }
                    }

                    &-last {
                        &:before {
                            left: 0;
                        }
                    }
                }

                @list: 1, 2, 3, 4, 5, 6, 7;
                each(@list, {
                    &_start-from-@{value} {
                        grid-column-start: @value;
                    }
                });
            }
        }

        &-footer {
            margin-top: 20px;

            &__field {
                display: grid;
                grid-template-columns: 1fr;
                grid-column-gap: var(--form-field-space);
                grid-row-gap: var(--form-field-space);

                &_range {
                    @media screen and (min-width: 480px) {
                        grid-template-columns: 1fr 1fr;
                    }
                }

                /deep/ input {
                    appearance: none;
                }
            }
        }
    }
</style>
