<template>
    <BaseField
        ref="field"
        v-bind="$attrs"
        :value="value"

        v-on="listeners"
        @validate="validate"
        @update:value="updateValue(true)"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>

        <template #service>
            <input
                :value="value_"
                :name="name"
                type="hidden"
            />
        </template>
    </BaseField>
</template>

<script>
import { IField } from './BaseField';

export default {
    name: 'FieldMask',
    extends: IField('field'),

    props: {
        name: String,
        mask: Object,
        value: [String, Number],
    },

    data() {
        return {
            value_: null,
        };
    },

    computed: {
        listeners() {
            const listeners = { ...this.$listeners };
            delete listeners['update:value'];

            return listeners;
        },
    },

    watch: {
        mask() {
            this._imask.updateOptions(this.mask);
        },

        value() {
            this.$nextTick(() => {
                this._imask.updateValue();
                this._imask.updateControl();
            });
        },
    },

    mounted() {
        import(/* webpackChunkName: "imask" */ 'imask').then((lib) => {
            const imask = lib.default;

            this._imask = imask(this.$refs.field.$refs.input, this.mask);
            this._pipe = imask.createPipe(this.mask, imask.PIPE_TYPE.MASKED, imask.PIPE_TYPE.UNMASKED);

            // Фикс одновременного blur библиотеки и нашего
            this._imask.on('accept', () => {
                if (!this.hasFocus()) this.$emit('validate');
            });

            this.updateValue(false);
        });
    },

    beforeDestroy() {
        this._imask.destroy();
    },

    methods: {
        updateValue(emit) {
            this.$nextTick(() => {
                this.value_ = this.getValue();

                if (emit) {
                    this.$emit('update:value', this.value_);
                }
            });
        },

        getValue() {
            return this._pipe(this.$refs.field.getValue());
        },

        validate() {
            return this.$refs.field.validate(this.getValue());
        },
    },
};
</script>
