import moment from 'moment';
import throttle from 'lodash/throttle';

import Format from './Format';
import Device from './Device';
import Message from './Message';

export default {
    install(Vue) {
        Vue.mixin({
            filters: {
                formatDeclensionWord: Format.declensionWord,
                formatNumber: Format.number,
                formatMoney: Format.money,
                formatDatetime: Format.datetime,
                formatPhone: Format.phone,
                formatCard: Format.card,
                formatNbsp: Format.nbsp,
                formatPercent: Format.percent,
                formatText: Format.text,
            },

            methods: {
                $getRandomString() {
                    return Math.random().toString(36).substring(2);
                },

                $getCharacterLength(str) {
                    return [...str].length;
                },

                $alert(message = '') {
                    alert(message);
                },

                $animate(el, animationClass, callback) {
                    el.addEventListener('animationend', clear);
                    el.classList.add(animationClass);

                    function clear() {
                        el.classList.remove(animationClass);
                        if (callback) callback(el);

                        el.removeEventListener('animationend', clear);
                    }
                },
            },
        });

        Vue.prototype.$format = Format;
        Vue.prototype.$device = Device;
        Vue.prototype.$message = Message;

        moment.locale('ru');
        Vue.prototype.$moment = moment;

        Vue.prototype.$_ = {
            throttle,
        };

        if (Device.isIOS()) {
            setCSSVhVar();
            window.addEventListener('resize', throttle(setCSSVhVar, 10));
        }

        setScrollWidthVar();
    },
};

function setCSSVhVar() {
    const vh = window.innerHeight * 0.01;
    document.documentElement.style.setProperty('--vh', `${vh}px`);
}

function setScrollWidthVar() {
    const div = document.createElement('div');

    div.style.overflowY = 'scroll';
    div.style.width = '50px';
    div.style.height = '50px';

    document.body.append(div);

    const scrollWidth = div.offsetWidth - div.clientWidth;
    document.documentElement.style.setProperty('--scroll-width', `${scrollWidth}px`);

    div.remove();
}
