<template>
    <div class="progress">
        <div
            v-if="labelStart || labelFinish"
            class="progress__label"
        >
            <div class="progress__label-start">{{ labelStart }}</div>
            <div class="progress__label-finish">{{ labelFinish }}</div>
        </div>
        <div class="progress__background">
            <div
                class="progress__line"
                :class="{ progress__line_full: fullIndicate && clearValue === 100 }"
                :style="{ width: `${clearValue}%` }"
            ></div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'BaseProgress',

    props: {
        value: Number,
        labelStart: String,
        labelFinish: String,

        fullIndicate: Boolean,
    },

    computed: {
        clearValue() {
            return this.value < 100 ? this.value : 100;
        },
    },

    watch: {
        value() {
            if (this.value < 100) return;

            // 500 - значение transition в CSS
            setTimeout(() => this.$emit('full'), 1000);
        },
    },
};
</script>

<style scoped lang="less">
    .progress {
        &__background {
            background: var(--ui__progress_background);
            height: 5px;
            overflow: hidden;
        }

        &__background,
        &__line {
            border-radius: 3px;
        }

        &__line {
            height: inherit;
            position: relative;
            background: var(--ui__progress_line_background);
            animation: onload-progress-animation 1s ease;

            // 500 - значение используется в JS
            transition: all 1000ms cubic-bezier(0.215, 0.61, 0.355, 1);

            &:after {
                content: '';
                display: block;
                position: absolute;
                top: 0;
                right: 0;
                bottom: 0;
                width: 100px;
                background: linear-gradient(to right, rgba(0, 0, 0, 0) 0%, rgba(0, 0, 0, 0.2) 100%);
            }

            &_full {
                background: var(--green);
            }
        }

        &__label {
            display: flex;
            color: var(--text_type_secondary);
            font-size: .8em;
            margin-bottom: 3px;

            &-start {
                flex-grow: 1;
            }
        }
    }

    @keyframes onload-progress-animation {
        from {
            transform: translate(-100%);
        }

        to {
            transform: translate(0);
        }
    }
</style>
