<template>
    <div class="checkbox">
        <label
            class="checkbox__element"
            :class="[
                `checkbox__element_type_${this.toggle ? 'toggle' : 'classic'}`,
                { checkbox__element_error: error, checkbox__element_disabled: disabled }
            ]"
        >
            <input
                ref="input"

                v-bind="$attrs"
                type="checkbox"
                class="checkbox__input sr-only"
                :disabled="disabled"
                :checked="checked_"

                @invalid.prevent="setError"
                @change="onChange"
            >
            <span
                class="checkbox__box"
                :class="{ checkbox__box_checked: checked_ }"
            >
                <ElementCheck
                    v-if="!toggle"
                    :active="checked_"
                    class="checkbox__check"
                />
            </span>
            <span
                v-if="label || $slots.label || $slots.message || error"
                class="checkbox__content"
            >
                <span
                    v-if="label || $slots.label"
                    class="checkbox__label"
                >
                    <slot name="label">
                        {{ label }}
                    </slot>
                </span>
                <BaseMessage
                    v-if="$slots.message"
                    v-show="!error"
                >
                    <slot name="message"/>
                </BaseMessage>
                <BaseMessage
                    v-show="error"
                    type="danger"
                >
                    {{ error }}
                </BaseMessage>
            </span>
        </label>
    </div>
</template>

<script>
export default {
    name: 'BaseCheckbox',

    components: {
        ElementCheck: () => import('./ElementCheck'),
    },

    inheritAttrs: false,

    props: {
        label: String,
        checked: Boolean,
        disabled: Boolean,
        toggle: Boolean,
    },

    data() {
        return {
            checked_: this.checked,
            error: null,
        };
    },

    methods: {
        setError() {
            this.error = 'Пожалуйста, поставьте отметку в данном поле для продолжения.';
        },

        onChange(e) {
            this.checked_ = e.target.checked;

            this.error = null;
            this.$emit('change', e.target.checked);
        },

        setChecked(value) {
            this.checked_ = value;
            this.$refs.input.checked = value;
        },
    },
};
</script>

<style scoped lang="less">
    .checkbox {
        &__element {
            position: relative;
            cursor: pointer;
            display: flex;
            align-items: center;

            &_disabled {
                pointer-events: none;

                .checkbox__box {
                    opacity: .5;
                }
            }
        }

        &__element_type_classic&__element_error &__box {
            box-shadow: 0 0 0 1px var(--ui__input_border_error) inset;
        }

        &__element_type_classic & {
            &__box {
                width: ~'calc(1.2rem + 2px)';
                height: $width;
                border-radius: 5px;
                background: var(--ui__input_background);
                box-shadow: 0 0 0 1px var(--ui__input_border) inset;
            }

            &__content {
                margin-left: 10px;
            }
        }

        &__element_type_classic &__input:focus + &__box {
            box-shadow: 0 0 0 1px var(--ui__input_border_focus) inset;
        }

        &__element_type_classic /deep/ &__check {
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
        }

        &__element_type_toggle {
            flex-flow: row-reverse;
        }

        &__element_type_toggle & {
            &__box {
                border-radius: 9999px;
                width: 2.4rem;
                padding: 2px;
                background: var(--ui__checkbox_toggle_background);
                transition: all 100ms linear;

                &:before {
                    content: '';
                    display: block;
                    width: 1.2rem;
                    height: $width;
                    background: var(--ui__checkbox_toggle_switch_background);
                    border-radius: 50%;
                    transition: all 100ms linear;
                    box-shadow:
                        0 3px 8px rgba(0, 0, 0, .15),
                        0 3px 1px rgba(0, 0, 0, .06),
                        0 0 0 .5px rgba(0, 0, 0, .04) inset;
                }

                &_checked {
                    background: var(--ui__input_background_active);

                    &:before {
                        transform: translate3d(calc(100% - 4px), 0, 0);
                    }
                }
            }

            &__content {
                margin-right: 20px;
            }
        }

        &__element_type_toggle &__input:focus + &__box {
            background: var(--ui__checkbox_toggle_background_focus);
        }

        &__element_type_toggle &__input:focus + &__box_checked {
            background: var(--ui__button_primary_background_active);
        }

        &__content {
            flex-grow: 1;
        }

        &__box {
            display: inline-block;
            transition: all 100ms linear;
            position: relative;
            flex-shrink: 0;
        }

        &__label {
            display: block;
        }
    }
</style>
