import { snackbarInstance } from '../components/TheSnackbar';

export default class Message {
    static success(text) {
        snackbarInstance.success(text);
    }

    static error(text) {
        snackbarInstance.error(text);
    }

    static notify(text) {
        snackbarInstance.notify(text);
    }

    static process(text, action) {
        snackbarInstance.process(text, action);
    }
}
