<template>
    <div
        class="expand-text"
        :style="{ '--max-lines': maxLines }"
    >
        <div
            ref="wrapper"
            class="expand-text__wrapper"
            :style="!expanded ? { maxHeight: `${1.5 * maxLines}em` } : null"
        >
            <div
                ref="content"
                class="expand-text__content"
                :class="{ 'expand-text__content_not-expanded': expandable && !expanded }"
            >
                <slot/>
            </div>
        </div>
        <div class="expand-text__action">
            <BaseLink
                v-if="expandable && !expanded"
                @click="onExpand"
            >
                Показать полностью...
            </BaseLink>
        </div>
    </div>
</template>

<script>
export default {
    name: 'BaseExpandText',

    props: {
        maxLines: {
            type: [String, Number],
            default: 4,
        },
    },

    data() {
        return {
            expandable: false,
            expanded: false,
        };
    },

    mounted() {
        if (this.$refs.wrapper.offsetHeight < this.$refs.content.offsetHeight) {
            this.expandable = true;
        }
    },

    methods: {
        onExpand() {
            this.expanded = true;
            this.$emit('expand');
        },
    },
};
</script>

<style scoped lang="less">
    .expand-text {

        &__wrapper {
            overflow: hidden;
        }

        &__content {

            &_not-expanded {
                @gradient-size: 2;
                --gradient-start: ~'calc((1 - @{gradient-size} / var(--max-lines)) * 100%)';

                mask-size: 100% ~'calc(var(--max-lines) * 1.5em)';
                mask-image: linear-gradient(#000 var(--gradient-start), transparent);
            }
        }
    }
</style>
