<template>
    <div class="tooltip">
        <div
            ref="caller"
            class="tooltip__caller"
            @click="show_ = true"
        >
            <slot>
                <span
                    v-if="label"
                    class="tooltip__caller-text"
                >
                    {{ label }}
                </span>
                <BaseButton
                    v-else

                    mode="rounded"
                    size="small"
                    type="secondary"

                    class="tooltip__caller-button"
                >
                    <font-awesome-icon icon="question"/>
                </BaseButton>
            </slot>
        </div>
        <UtilPopup
            :show.sync="show_"
            :corner="true"
            :offset-x="-15"
            :parent="$refs.caller"

            corner-custom-class="tooltip__corner"
        >
            <div
                ref="tooltip"
                class="tooltip__block"
            >
                {{ content }}
            </div>
        </UtilPopup>
    </div>
</template>

<script>
import UtilPopup from './UtilPopup';

export default {
    name: 'BaseTooltip',
    components: { UtilPopup },

    props: {
        label: String,
        content: String,
        show: Boolean,
    },

    data() {
        return {
            show_: this.show,
        };
    },
};
</script>

