<template>
    <div class="dropdown">
        <div
            ref="parent"

            class="dropdown__parent"
            @click="showElement"
        >
            <slot/>
        </div>
        <BaseModal
            :show.sync="showModal"
            :close-button="false"
            card
        >
            <BaseLink
                v-for="option in options"

                v-bind="option.attrs"
                class="dropdown__element"
                :as-text="true"

                @click="onClick(option, $event)"
            >
                {{ option.text }}
            </BaseLink>
            <div class="dropdown__close-button">
                <BaseButton
                    type="secondary"
                    mode="block"
                    @click="showModal = false"
                >
                    Отмена
                </BaseButton>
            </div>
        </BaseModal>
        <UtilPopup
            :show.sync="showPopup"
            :parent="$refs.parent"
        >
            <div class="dropdown__popup">
                <BaseLink
                    v-for="option in options"

                    v-bind="option.attrs"
                    class="dropdown__element"
                    :as-text="true"

                    @click="onClick(option, $event)"
                >
                    {{ option.text }}
                </BaseLink>
            </div>
        </UtilPopup>
    </div>
</template>

<script>
import UtilPopup from './UtilPopup';

export default {
    name: 'BaseDropdown',
    components: { UtilPopup },

    props: {
        options: Array,
    },

    data() {
        return {
            showModal: false,
            showPopup: false,
        };
    },

    methods: {
        onClick(option, e) {
            if (option.click) {
                option.click(option, e);
            }

            this.$emit('select', option);

            this.showModal = false;
            this.showPopup = false;
        },

        showElement() {
            if (this.$device.isDesktop()) {
                this.showPopup = true;
                return;
            }

            this.showModal = true;
        },
    },
};
</script>

<style lang="less">
    .dropdown__element {
        display: block;
        padding: 10px;

        @media screen and (max-width: 480px) {
            margin: 0 ~'calc(var(--content-padding) * -1)';
            padding-left: var(--content-padding);
            padding-right: var(--content-padding);

            &:not(:first-child) {
                margin-top: 5px;
            }
        }

        @media screen and (min-width: 480px) {
            border-radius: 5px;
        }

        .element-animation();
        .button-tertiary-interaction();
    }
</style>

<style scoped lang="less">
    .dropdown {
        &__close-button {
            margin-top: 20px;
        }

        &__parent {
            display: inline-block;
            cursor: pointer;
