<template>
    <div
        class="tag"
        :class="{ tag_selectable: !removable, tag_selected: selected_ }"
        @click="onSelect"
    >
        {{ text }}<slot/>
        <div
            v-if="removable"
            class="tag__icon"
            @click="$emit('remove')"
        >
            <font-awesome-icon icon="times"/>
        </div>
    </div>
</template>

<script>
export default {
    name: 'BaseTag',

    props: {
        removable: Boolean,
        selected: Boolean,
        text: String,
    },

    data() {
        return {
            selected_: this.selected,
        };
    },

    methods: {
        onSelect() {
            if (this.removable) return;

            this.selected_ = !this.selected_;
            this.$emit('select', this.selected_);
        },
    },
};
</script>

<style scoped lang="less">
    .tag {
        display: inline-flex;
        padding: 8px 10px;
        user-select: none;
        line-height: 1;
        border-radius: 5px;
        background: var(--ui__button_secondary_background);
        color: var(--ui__button_secondary_color);
        overflow: hidden;
        font-size: .85em;

        &_selectable {
            cursor: pointer;
            .element-animation();
            .button-secondary-interaction();
        }

        &_selected {
            .button-primary-interaction();
        }

        &__icon {
            cursor: pointer;
            opacity: .5;
            margin: -8px -10px -8px 0;
            padding: 8px 10px;
            display: flex;
            align-items: center;

            svg {
                display: block;
            }
        }
    }
</style>
