<template>
    <UtilPortal
        v-if="show_"
        :id="portalData.targetId || null"
    >
        <div
            ref="popup"
            class="popup__element popup__element_no-display animation faster"
            :style="{ left: `${shiftX}px`, top: `${shiftY}px`, '--popups-count': popupsCount }"

            @click.stop
        >
            <div
                v-if="corner"
                ref="corner"
                class="popup__corner"
                :class="[`popup__corner_popup-align_${alignY_}`, cornerCustomClass]"
                :style="{ left: `${shiftCorner}px` }"
            >
                <svg width="20px" height="8px" viewBox="0 0 20 8" version="1.1" xmlns="http://www.w3.org/2000/svg">
                    <path d="M10,0 C13.5,0 15.5,8 20,8 L0,8 C4.5,8 6.5,0 10,0 Z" fill="currentColor"></path>
                </svg>
            </div>
            <slot/>
        </div>
    </UtilPortal>
</template>

<script>
import UtilPortal from './UtilPortal';

let popupsCount = 0;

const ANIMATIONS = {
    top: {
        enter: 'slide-from-bottom',
        leave: 'slide-to-bottom',
    },
    bottom: {
        enter: 'slide-from-top',
        leave: 'slide-to-top',
    },
};

export default {
    name: 'UtilPopup',
    components: { UtilPortal },

    props: {
        show: Boolean,
        corner: Boolean,
        cornerCustomClass: String,
        alignY: String,

        offsetX: {
            type: Number,
            default: 0,
        },
        offsetY: {
            type: Number,
            default: 5,
        },

        parent: HTMLDivElement,
        block: Boolean,
    },

    data() {
        return {
            show_: false,
            shiftX: 0,
            shiftY: 0,
            shiftCorner: 0,

            alignY_: null,
            zIndex: 0,
            popupsCount: 0,

            portalData: {},
        };
    },

    watch: {
        show() {
            this.toggle(this.show);
        },
    },

    // Проверяем есть ли перезапись стандартного портала для верного отображения попапа
    created() {
        let parent = this.$parent;

        while (true) {
            if (!parent) break;

            if (parent.portalOverride) {
                this.portalData = parent.portalOverride;
                break;
            }

            parent = parent.$parent;
        }
    },

    beforeDestroy() {
        document.removeEventListener('click', this.clickOutside);
    },

    methods: {
        toggle(show) {
            if (this.show_ === show) return;

            if (!show) {
                document.removeEventListener('click', this.clickOutside);

                this.$animate(this.$refs.popup, ANIMATIONS[this.alignY_].leave, () => {
                    popupsCount -= 1;
                    this.show_ = false;
                    this.$emit('hidden');
                });

                return;
            }

            popupsCount += 1;
            this.popupsCount = popupsCount;

            this.show_ = show;

            const tick = () => {
                if (!this.$refs.popup) {
                    setTimeout(tick, 1);
                    return;
                }

                this.setPosition();
                this.setWidth();

                this.$emit('ready');

                this.$refs.popup.classList.remove('popup__element_no-display');

                this.$animate(this.$refs.popup, ANIMATIONS[this.alignY_].enter, () => {
                    document.addEventListener('click', this.clickOutside);
                    this.$emit('visible');
                });
            };
            tick();
        },

        setWidth() {
            if (!this.block) return;

            const $parent = this.parent;
            const $popup = this.$refs.popup;

            const popupWidth = $popup.getBoundingClientRect().width;
            const parentWidth = $parent.getBoundingClientRect().width;

            if (popupWidth < parentWidth) {
                $popup.style.width = `${parentWidth}px`;
            }
        },

        setPosition() {
            const $allowedArea = Object.keys(this.portalData).length === 0 ? this.$root.$el : this.portalData.getSafeAreaBlock();
            const $parent = this.parent;
            const $popup = this.$refs.popup;
            const $corner = this.$refs.corner;

            const SAFE_AREA = 10;

            const allowedAreaRect = $allowedArea.getBoundingClientRect();
            const parentRect = $parent.getBoundingClientRect();
            const popupRect = $popup.getBoundingClientRect();

            let shiftX = window.pageXOffset + (parentRect.left - allowedAreaRect.left) + this.offsetX;

            shiftX = Math.max(SAFE_AREA, shiftX);
            shiftX = Math.min(shiftX, allowedAreaRect.width - popupRect.width - SAFE_AREA);

            this.shiftX = shiftX;

            const { clientHeight } = document.documentElement;
            let { offsetY } = this;

            if ($corner) {
                const cornerRect = $corner.getBoundingClientRect();
                offsetY += cornerRect.height;

                this.shiftCorner = parentRect.left - allowedAreaRect.left - this.shiftX + parentRect.width / 2 - cornerRect.width / 2;
            }

            let shiftY = window.pageYOffset;
            if (Object.keys(this.portalData).length !== 0) {
                const $scrollBlock = this.portalData.getScrollBlock();
                shiftY = $scrollBlock.scrollTop + $scrollBlock.getBoundingClientRect().top * -1;
            }

            // Math.round() для фикса уголка в ios
            if (this.alignY === 'top' || (parentRect.bottom + offsetY + popupRect.height > clientHeight && parentRect.top - offsetY - popupRect.height > 0)) {
                this.shiftY = Math.round(shiftY + parentRect.top - offsetY - popupRect.height);
                this.alignY_ = 'top';
            } else {
                this.shiftY = Math.round(shiftY + parentRect.bottom + offsetY);
                this.alignY_ = 'bottom';
            }
        },

        clickOutside(e) {
            // const $popup = this.$refs.popup;
            //
            // if ($popup !== e.target && !$popup.contains(e.target)) {
            //     this.$emit('update:show', false);
            // }

            this.$emit('update:show', false);
        },
    },
};
</script>

<style scoped lang="less">
    .popup {
        display: inline-block;

        &__element {
            // Минус SAFE_AREA * 2
            max-width: calc(100% - 20px);
            z-index: ~'calc(var(--level-popup) + var(--popups-count))';
            position: absolute;

            // Фикс отображения состояния блока перед добавлением класса анимации
            &_no-display {
                opacity: 0;
            }
        }

        &__corner {
            position: absolute;
            width: 20px;
            height: 8px;

            & > svg {
                position: absolute;
            }

            &_popup-align {
                &_top {
                    bottom: -8px;
                    transform: rotate(180deg);
                }

                &_bottom {
                    top: -8px;
                }
            }
        }
    }
</style>
