<template>
    <BaseField
        ref="field"

        v-bind="$attrs"
        :value="dateFormatted"
        readonly

        v-on="$listeners"
        @focus="showCalendar = true"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>

        <template #uiStart>
            <font-awesome-icon :icon="['far', 'calendar-alt']"/>
        </template>

        <template #service>
            <ModalDatepicker
                :show.sync="showCalendar"
                :start="rangeStart || start"
                :end="rangeEnd || end"
                :min="min"
                :max="max"
                :range="range"
                :timepicker="timepicker"
                @select="onSelect"
            />

            <input
                v-if="rangeStart"
                :value="rangeStart.unix()"
                :name="name + (range ? '_start' : '')"
                type="hidden"
            />
            <input
                v-if="rangeEnd"
                :value="rangeEnd.unix()"
                :name="name + '_end'"
                type="hidden"
            />
        </template>
    </BaseField>
</template>

<script>
import ModalDatepicker from './ModalDatepicker';
import { IField } from './BaseField';

export default {
    name: 'FieldDatetime',
    components: { ModalDatepicker },
    extends: IField('field'),

    inheritAttrs: false,
    props: {
        name: String,
        start: [Object, String, Number],
        end: [Object, String, Number],
        min: Object,
        max: Object,
        range: Boolean,
        timepicker: Boolean,
    },

    data() {
        return {
            showCalendar: false,
            rangeStart: this.getInitValue(this.start),
            rangeEnd: this.getInitValue(this.end),
            format: `DD MMMM YYYY${this.timepicker ? ' в HH:mm' : ''}`,
        };
    },

    computed: {
        dateFormatted() {
            if (!this.rangeStart) return;

            let dates = [
                this.rangeStart,
                ...(this.rangeEnd ? [this.rangeEnd] : []),
            ];

            dates = dates.map((date) => date.format(this.format));
            return [...new Set(dates)].join(' — ');
        },
    },

    methods: {
        onSelect(range) {
            this.rangeStart = range.rangeStart;
            this.rangeEnd = range.rangeEnd;
        },

        getInitValue(value) {
            switch (true) {
            case !value:
                return null;

            case this.$moment.isMoment(value):
                return value;

            case /\d{10}(\.\d{1,3})?/.test(value.toString()):
                return this.$moment.unix(value);
            }

            return this.$moment(value);
        },
    },
};
</script>
