<template>
    <div
        class="field"
        v-on="$listeners"
    >
        <label
            v-if="$slots.label"
            class="field__label"
            :for="id"
            @click="$emit('update:focus', true)"
        >
            <BaseLabel :error="!!error">
                <slot name="label"/>
            </BaseLabel>
        </label>
        <div
            ref="fieldArea"

            class="field__area"
            :class="[
                `field__area_size_${size}`,
                `field__area_type_${appearance}`,
                { field__area_focus: focus, field__area_error: error, field__area_disabled: disabled }
            ]"

            @click="$emit('update:focus', true)"
            @mousedown="preventBlur"
        >
            <div
                v-if="$slots.actionStart"
                class="field__ui"
            >
                <slot name="actionStart"/>
            </div>
            <div
                v-if="$slots.uiStart"
                class="field__ui field__ui_icon"
            >
                <slot name="uiStart"/>
            </div>
            <div
                ref="field"
                class="field__ui field__ui_center"
            >
                <slot/>
            </div>
            <div
                v-if="$slots.uiEnd"
                class="field__ui field__ui_icon"
            >
                <slot name="uiEnd"/>
            </div>
            <div
                v-if="$slots.actionEnd"
                class="field__ui"
            >
                <slot name="actionEnd"/>
            </div>
        </div>
        <div
            v-show="error"
            class="field__message field__message_error"
        >
            {{ error }}
        </div>
        <div
            v-if="$slots.message"
            v-show="!error"
            class="field__message"
        >
            <slot name="message"/>
        </div>
        <slot name="service"/>
    </div>
</template>

<script>
export default {
    name: 'UtilField',

    props: {
        id: String,
        size: {
            type: String,
            default: 'large',
        },
        appearance: {
            type: String,
            default: 'default',
        },
        focus: Boolean,
        error: String,
        disabled: Boolean,
    },

    methods: {
        preventBlur(e) {
            const $el = this.$refs.field;
            if (this.focus && !($el == e.target || $el.contains(e.target))) e.preventDefault();
        },
    },
};
</script>

<style scoped lang="less">
    .field {
        //width: 100%;
        flex-grow: 1;

        &__area {
            flex-grow: 1;
            border-radius: 5px;
            transition: all 100ms linear;
            cursor: text;
            display: flex;
            align-items: center;
            padding: 0 var(--padding-x);

            &_size_large {
                --padding-x: 12px;
                --padding-y: 10px;
            }

            &_size_medium {
                --padding-x: 7px;
                --padding-y: 4px;
            }

            &_type_default {
                background: var(--ui__input_background);
                box-shadow: 0 0 0 1px var(--ui__input_border) inset;
            }

            &_type_gray {
                background: var(--ui__input_background_secondary);
            }

            &_focus {
                box-shadow: 0 0 0 1px var(--ui__input_border_focus) inset;
            }

            &_error:not(&_focus) {
                box-shadow: 0 0 0 1px var(--ui__input_border_error) inset;
            }

            &_disabled {
                opacity: .5;
                pointer-events: none;
            }
        }

        &__label {
            display: inline-block;
            margin-bottom: .25rem;
        }

        &__message {
            font-size: .8em;
            color: var(--text_type_secondary);
            margin-top: .25rem;

            &_error {
                color: var(--text_error);
            }
        }

        &__ui {
            position: relative;

            &:not(:first-child) {
                margin-left: ~'calc(var(--padding-x) * 0.65)';
            }

            &_icon {
                color: var(--text_type_tertiary);
            }

            &_center {
                flex-grow: 1;
                min-height: 1.5em;
                margin-top: var(--padding-y);
                margin-bottom: var(--padding-y);
            }

            /deep/ .button {
                //display: block;
            }
        }
    }
</style>
