<template functional>
    <component
        :is="props.submit ? 'button' : 'div'"
        :ref="data.ref"

        class="button"
        :class="[
            data.class,
            data.staticClass,
            `button_size_${props.size}`,
            `button_type_${props.type}`,
            `button_mode_${props.mode}`,
            { button_loading: props.loading, button_disabled: props.disabled }
        ]"
        :style="[data.style, data.staticStyle]"
        :tabindex="props.loading ? -1 : null"
        :type="props.submit ? 'submit' : null"
        :disabled="disabled"

        v-on="listeners"
    >
        <span
            v-if="slots().prepend"
            class="button__icon"
        >
            <slot name="prepend"/>
        </span>
        <span
            v-if="props.label || slots().default"
            class="button__label"
        >
            <slot/>
            <template v-if="props.label">
                {{ props.label }}
            </template>
        </span>
        <span
            v-if="slots().append"
            class="button__icon"
        >
            <slot name="append"/>
        </span>
    </component>
</template>

<script>
export default {
    name: 'BaseButton',

    props: {
        label: String,
        loading: Boolean,
        submit: Boolean,
        disabled: Boolean,

        size: {
            type: String,
            default: 'large',
        },

        type: {
            type: String,
            default: 'primary',
        },

        mode: {
            type: String,
            default: '',
        },
    },
};
</script>

<style scoped lang="less">
    .button {
        text-align: center;
