        position: relative;
        text-decoration: none !important;
        font-weight: 500;
        border: 0;
        outline: 0 !important;
        line-height: 1;
        white-space: nowrap;
        user-select: none;
        display: inline-flex;
        align-items: center;
        justify-content: center;
        border-radius: 9999px;

        .element-animation();

        /* Размеры */

        &_size {
            &_small {
                font-size: .8em;
                padding: 7px 12px;
            }

            &_medium {
                padding: 10px 16px;
            }

            &_large {
                padding: 12px 20px;
            }
        }

        /* Модификации */

        &_mode_block {
            width: 100%;
        }

        &_mode_rounded&_size {
            &_small,
            &_medium,
            &_large {
                padding: 0;
            }

            &_small {
                width: ~'calc(12px + 1.2em)';
                height: $width;
            }

            &_medium {
                width: ~'calc(16px + 1.22em)';
                height: $width;
            }

            &_large {
                width: ~'calc(20px + 1.25em)';
                height: $width;
            }
        }

        /* Типы */

        &_type {
            &_primary {
                .button-primary-interaction();
            }

            &_secondary {
                .button-secondary-interaction();
            }

            &_tertiary {
                .button-tertiary-interaction();
            }
        }

        /* Состояния */

        &_loading {
            pointer-events: none;
            color: transparent;

            &:after {
                position: absolute;
                display: block;
                content: '';
                z-index: 10;
                top: 50%;
                left: 50%;
                border: 2px solid;
                border-color: rgba(0, 0, 0, .1) rgba(0, 0, 0, .1) rgba(0, 0, 0, .1) #fff;
                border-radius: 50%;
                animation: button-load 1s linear infinite;
            }

        }

        &_loading&_size {
            &_small:after {
                width: 14px;
                height: $width;
                margin: -$width/2 0 0 -$width/2;
            }

            &_medium:after {
                width: 18px;
                height: $width;
                margin: -$width/2 0 0 -$width/2;
            }

            &_large:after {
                width: 22px;
                height: $width;
                margin: -$width/2 0 0 -$width/2;
            }
        }

        &_disabled {
            pointer-events: none;
        }

        &::-moz-focus-inner {
            border: 0;
        }

        &__label,
        &__icon {
            display: block;
        }

        &__icon {
            opacity: .5;
        }

        &__icon + &__label,
        &__label + &__icon {
            margin-left: 5px;
        }

        /deep/ svg {
            display: block;
        }
    }

    @keyframes button-load {
        from {
            transform: rotate(0deg);
        }
        to {
            transform: rotate(360deg);
        }
    }
</style>
