<template>
    <form
        class="form"
        @submit.prevent="submit"
    >
        <slot/>
    </form>
</template>

<script>
export default {
    provide() {
        return {
            $loading: () => this.loading,
            $error: () => this.error,
            $fields: () => this.fields,
        };
    },

    name: 'BaseForm',

    props: {
        action: String,
        method: {
            type: String,
            default: 'POST',
        },
    },

    data() {
        return {
            fields: {},
            loading: false,
            error: false,
        };
    },

    methods: {
        submit() {
            if (this.loading) return;

            this.error = false;

            this.$nextTick(() => {
                this.error = !Object.values(this.fields).reduce((previousValue, field) => {
                    if (field.hasOwnProperty('validate') && !field.validate()) {
                        // console.log(field.$options.name);
                        return false;
                    }
                    return previousValue;
                }, true);

                if (this.error) return;

                if (this.$listeners.submit) {
                    this.loading = true;

                    const formData = new FormData(this.$el);
                    const finishLoading = () => this.loading = false;

                    this.$emit('submit', formData, finishLoading);
                }
            });
        },
    },
};
</script>

