import './styles/styles.less';

import Vue from 'vue';
import { library } from '@fortawesome/fontawesome-svg-core';
import {
    faEye, faEyeSlash, faChevronLeft, faChevronRight, faChevronDown, faQuestion, faTimes, faHeart, faCheck, faPlus, faEllipsisH,
} from '@fortawesome/free-solid-svg-icons';
import { faSmile, faCalendarAlt } from '@fortawesome/free-regular-svg-icons';
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome';
import App from './App.vue';
import router from './router';
import store from './store';

import vueExtendPlugin from './js/vueExtendPlugin';

Vue.config.productionTip = false;

library.add(faEye, faEyeSlash, faCalendarAlt, faChevronLeft, faChevronRight, faChevronDown, faQuestion, faTimes, faSmile, faHeart, faCheck, faPlus, faEllipsisH);
Vue.component('font-awesome-icon', FontAwesomeIcon);

const requireComponent = require.context('./components', false, /Base[A-Z]\w+\.(vue|js)$/);
requireComponent.keys().forEach((fileName) => {
    const componentConfig = requireComponent(fileName);
    const componentName = fileName.split('/').pop().replace(/\.\w+$/, '');

    Vue.component(componentName, componentConfig.default || componentConfig);
});

Vue.use(vueExtendPlugin);

new Vue({
    router,
    store,
    render: (h) => h(App),
}).$mount('#app');
