<template>
    <component
        :is="fieldType"
        ref="field"
        :name="name"
        class="form__row"
        v-bind="$attrs"
        v-form-field="{ name, fields }"
        v-on="$listeners"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>
    </component>
</template>

<script>
import { IField } from './BaseField';
import FieldCard from './FieldCard';
import FieldDatetime from './FieldDatetime';
import FieldEmail from './FieldEmail';
import FieldMask from './FieldMask';
import FieldNumber from './FieldNumber';
import FieldPhone from './FieldPhone';
import FieldSearch from './FieldSearch';
import FieldSelect from './FieldSelect';
import FieldTextarea from './FieldTextarea';
import FieldUrl from './FieldURL';

export default {
    name: 'BaseFormField',
    components: {
        FieldCard,
        FieldDatetime,
        FieldEmail,
        FieldMask,
        FieldNumber,
        FieldPassword: () => import(/* webpackChunkName: "FieldPassword" */ './FieldPassword'),
        FieldPhone,
        FieldSearch,
        FieldSelect,
        FieldTextarea,
        FieldUrl,
    },
    inject: ['$fields'],

    directives: {
        formField: {
            // определение директивы
            inserted(el, binding, vnode) {
                const { name, fields } = binding.value;

                if (!name) return;
                fields[name] = vnode.componentInstance;
            },

            unbind(el, binding, vnode) {
                const { name, fields } = binding.value;

                if (name) {
                    delete fields[name];
                }
            },
        },
    },

    extends: IField('field'),

    props: {
        type: String,
        name: String,
    },

    computed: {
        fields() {
            return this.$fields();
        },

        fieldType() {
            if (!this.type) return 'BaseField';
            return `Field${capitalizeFirstLetter(this.type)}`;

            function capitalizeFirstLetter(string) {
                return string.charAt(0).toUpperCase() + string.slice(1);
            }
        },

        field() {
            return this.$refs.field;
        },
    },
};
</script>

