import Vue from 'vue';
import VueRouter from 'vue-router';

Vue.use(VueRouter);

const routes = [
    {
        path: '/',
        name: 'showcase',
        component: () => import(/* webpackChunkName: "Showcase" */ '../views/Showcase.vue'),
    },
    {
        path: '/main',
        component: () => import(/* webpackChunkName: "Main" */ '../views/Main.vue'),

        children: [
            {
                path: '',
                redirect: 'stats',
            },
            {
                path: 'stats',
                component: () => import(/* webpackChunkName: "MainStats" */ '../views/MainStats.vue'),
            },
            {
                path: 'withdraw',
                component: () => import(/* webpackChunkName: "MainWithdraw" */ '../views/MainWithdraw.vue'),
            },
        ],
    },
    {
        path: '/notifications',
        name: 'notifications',
        component: () => import(/* webpackChunkName: "notifications" */ '../views/Notifications.vue'),
    },
    {
        path: '/sponsors',
        name: 'sponsors',
        component: () => import(/* webpackChunkName: "sponsors" */ '../views/Sponsors.vue'),
    },
    {
        path: '/settings',
        component: () => import(/* webpackChunkName: "settings" */ '../views/Settings.vue'),

        children: [
            {
                path: '',
                redirect: 'levels',
            },
            {
                path: 'levels',
                component: () => import(/* webpackChunkName: "SettingsLevels" */ '../views/SettingsLevels.vue'),
            },
            {
                path: 'goals',
                component: () => import(/* webpackChunkName: "SettingsGoals" */ '../views/SettingsGoals.vue'),
            },
            {
                path: 'community',
                component: () => import(/* webpackChunkName: "SettingsCommunity" */ '../views/SettingsCommunity.vue'),
            },
            {
                path: 'admins',
                component: () => import(/* webpackChunkName: "SettingsAdmins" */ '../views/SettingsAdmins.vue'),
            },
        ],
    },
];

if(process.env.NODE_ENV === 'development') {
    routes.push({
        path: '/test',
        name: 'test',
        // route level code-splitting
        // this generates a separate chunk (about.[hash].js) for this route
        // which is lazy-loaded when the route is visited.
        component: () => import(/* webpackChunkName: "test" */ '../views/Test.vue'),
    });
}

const router = new VueRouter({
    mode: 'history',
    base: process.env.BASE_URL,
    routes,

    scrollBehavior(to, from, savedPosition) {
        if (savedPosition) {
            return savedPosition;
        }

        return { x: 0, y: 0 };
    },
});

export default router;
