<template>
    <BaseButton
        ref="button"

        v-bind="$attrs"
        :loading="loading"
        submit
        class="animation duration-800"

        v-on="$listeners"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>
    </BaseButton>
</template>

<script>
export default {
    name: 'BaseFormButton',
    inject: ['$loading', '$error'],

    computed: {
        loading() {
            return this.$loading();
        },
        error() {
            return this.$error();
        },
    },

    watch: {
        error() {
            if (this.error) {
                this.$animate(this.$refs.button, 'shake');
            }
        },
    },
};
</script>
