<template>
    <UtilField
        ref="utilField"

        :size="size"
        :focus.sync="focus"
        :error="error"
        :disabled="disabled"
        class="select"
    >
        <div
            ref="selectField"

            :tabindex="disabled ? -1 : 0"
            class="select__field"

            @keydown.up.prevent="moveTarget(-1)"
            @keydown.down.prevent="moveTarget(1)"
            @keydown.enter.prevent="select()"

            @focus="focus = true"
            @blur="focus = false"
        >
            <template v-if="selectedOptionsText">
                {{ selectedOptionsText }}
                <BaseMessage v-if="!multiple && selectedOptionsDescription">
                    {{ selectedOptionsDescription }}
                </BaseMessage>
            </template>
            <template v-else>
                <div class="select__field-placeholder">{{ placeholder }}</div>
            </template>
        </div>

        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>

        <template #uiEnd>
            <div
                class="select__field-arrow"
                :class="{ 'select__field-arrow_active': focus }"
            >
                <font-awesome-icon icon="chevron-down"/>
            </div>
        </template>

        <template #label>
            <slot name="label"/>
            <template v-if="label">
                {{ label }}
            </template>
        </template>

        <template #service>
            <UtilPopup
                ref="popup"

                :show="showPopup"
                :parent="parent"
                :block="true"

                @ready="prepareOptions"
            >
                <div
                    ref="options"
                    class="select__options"
                    @mousedown.prevent
                >
                    <UtilScrollbar
                        ref="scrollbar"
                        :max-height="optionsHeight"
                    >
                        <div
                            v-for="(option, index) in options"

                            :data-target="index"
                            :class="{ select__option_target: target == index }"
                            class="select__option"

                            @mouseover="target = index"
                            @click="select(option.value)"
                        >
                            <div
                                v-if="multiple"
                                class="select__option-check"
                            >
                                <ElementCheck :active="selected_.includes(option.value)"/>
                            </div>
                            <div class="select__option-text">
                                {{ option.text }}
                                <BaseMessage v-if="option.description">
                                    {{ option.description }}
                                </BaseMessage>
                            </div>
                        </div>
                        <div class="select__option_empty">
                            {{ emptyTargetsMessage }}
                        </div>
                    </UtilScrollbar>
                </div>
            </UtilPopup>

            <BaseModal
                :show.sync="showModal"
                :title="label"
            >
                <div
                    v-for="option in options"
                    class="select__option"
                    @click="select(option.value)"
                >
                    <span
                        v-if="multiple"
                        class="select__option-check"
                    >
                        <ElementCheck :active="selected_.includes(option.value)"/>
                    </span>
                    <div class="select__option-text">
                        {{ option.text }}
                        <BaseMessage v-if="option.description">
                            {{ option.description }}
                        </BaseMessage>
                    </div>
                </div>
                <div class="select__option_empty">
                    {{ emptyTargetsMessage }}
                </div>
            </BaseModal>

            <input
                :value="value"
                :name="name"
                type="hidden"
            />
        </template>
    </UtilField>
</template>

<script>
import UtilScrollbar from './UtilScrollbar';
import UtilField from './UtilField';
import UtilPopup from './UtilPopup';

export default {
    name: 'FieldSelect',

    components: {
        UtilPopup,
        UtilScrollbar,
        UtilField,
        ElementCheck: () => import('./ElementCheck'),
    },

    props: {
        size: String,
        disabled: Boolean,
        required: Boolean,

        name: String,
        placeholder: String,
        label: String,

        multiple: Boolean,
        options: Array,
        selected: {
            type: [String, Number, Array],
            default: () => [],
        },

        emptyTargetsMessage: {
            type: String,
            default: 'Нет доступных вариантов',
        },
    },

    data() {
        return {
            focus: false,
            error: null,

            target: 0,
            selected_: [],

            optionsHeight: 39.5 * 5,
            parent: null,

            showPopup: false,
            showModal: false,
        };
    },

    computed: {
        selectedOptions() {
            const options = this.options.filter((option) => this.selected_.includes(option.value));
            options.sort((a, b) => this.selected_.indexOf(a.value) - this.selected_.indexOf(b.value));
            return options;
        },

        selectedOptionsText() {
            return this.selectedOptions.map((x) => x.text).join(', ');
        },

        selectedOptionsDescription() {
            return this.selectedOptions.map((x) => x.description).join(', ');
        },

        value() {
            return this.selected_.join(',');
        },
    },

    watch: {
        focus() {
            if (this.focus) {
                this.$refs.selectField.focus();

                if (this.$device.isDesktop()) {
                    this.showPopup = true;
                } else {
                    this.showModal = true;
                }

                return;
            }

            this.showPopup = false;

            this.$refs.selectField.blur();
            this.validate();
        },

        // Перевод значений value в тип "строка" для корректного сравнения
        options: {
            handler() {
                this.options.forEach((option) => option.value = option.value.toString());
            },
            immediate: true,
        },
    },

    created() {
        this.setValue(this.selected);
    },

    mounted() {
        this.parent = this.$refs.utilField.$refs.fieldArea;
    },

    methods: {
        scrollToTarget() {
            const $option = this.$refs.options.querySelector(`[data-target='${this.target}']`);
            const $scrollElement = this.$refs.scrollbar;

            if (!$option) return;

            const scrollRect = $scrollElement.getBoundingClientRect();
            const optionRect = $option.getBoundingClientRect();

            const offsetTop = $option.offsetTop - $scrollElement.offsetTop;

            if (optionRect.bottom > scrollRect.bottom) {
                $scrollElement.scrollTop = offsetTop - (scrollRect.height - optionRect.height);
            }

            if (optionRect.top < scrollRect.top) {
                $scrollElement.scrollTop = offsetTop;
            }
        },

        prepareOptions() {
            this.target = Math.max(0, this.options.findIndex((option) => option.value === this.selected_[0]));

            const $option = this.$refs.options.querySelector(`[data-target='${this.target}']`);

            if (!$option) return;

            this.optionsHeight = $option.getBoundingClientRect().height * 5;
            this.scrollToTarget();
        },

        moveTarget(value) {
            if (this.options.length === 0) return;

            const index = this.target + value;
            const length = this.options.length - 1;

            this.target = index > length ? length : index == -1 ? 0 : index;
            this.scrollToTarget();
        },

        select(value) {
            if (!this.options.length) return;

            this.error = null;
            value = value || this.options[this.target].value;

            if (!this.multiple) {
                this.selected_ = [value];
                this.focus = false;
                this.showModal = false;
            } else {
                if (this.selected_.includes(value)) {
                    this.selected_.splice(this.selected_.indexOf(value), 1);
                    value = null;
                } else {
                    this.selected_.push(value);
                }

                if (this.showPopup) {
                    this.$nextTick(() => {
                        this.$refs.popup.setPosition();
                    });
                }
            }

            if (value) {
                this.$emit('select', value);
            }
        },

        getValue() {
            return this.selected_;
        },

        setValue(value) {
            if (!value) {
                this.selected_ = [];
                return;
            }

            this.selected_ = (Array.isArray(value) ? [...value] : [value]).map((v) => v = v.toString());
            this.error = null;
        },

        validate() {
            if (this.required && !this.value) {
                this.error = 'Пожалуйста, выберите значение из списка.';
                return false;
            }

            return true;
        },
    },
};
</script>

<style scoped lang="less">
    .select {

        /deep/ .field__area {
            cursor: pointer;
        }

        &__field {
            outline: none;

            &-placeholder {
                color: var(--text_type_tertiary);
            }

            &-arrow {
                transition: all 100ms linear;

                &_active {
                    color: var(--theme-color);
                }
            }
        }

        &__options {
            background: var(--ui__area_background);
            box-shadow: 0 2px 10px var(--ui__area_shadow);
            border: 1px solid var(--ui__area_border);
            border-radius: 5px;
            overflow: hidden;
        }

        &__option {
            cursor: pointer;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            display: flex;

            @media screen and (max-width: 480px) {
                .element-animation();
                .button-tertiary-interaction();
            }

            &_target {
                background: var(--ui__select_target_background);
            }

            &, &_empty {
                padding: 10px;

                @media screen and (max-width: 480px) {
                    margin: 0 ~'calc(var(--content-padding) * -1)';
                    padding-left: var(--content-padding);
                    padding-right: var(--content-padding);

                    &:not(:first-child) {
                        margin-top: 5px;
                    }
                }
            }

            &_empty:not(:first-child) {
                display: none;
            }

            &-check {
                display: inline-block;
                margin-right: 10px;
            }
        }
    }
</style>
