<template>
    <FieldMask
        ref="field"

        v-bind="$attrs"
        autocomplete="cc-number"
        placeholder="0000 0000 0000 0000"
        inputmode="numeric"
        minlength="16"
        :mask="{
            mask: '0000 0000 0000 0000'
        }"

        v-on="$listeners"
        @validate="validate"
        @update:value="updateCardImage"
    >
        <template v-for="(_, slot) of $slots" v-slot:[slot]>
            <slot :name="slot"/>
        </template>

        <template #actionStart v-if="cardImage">
            <div
                class="card"
                :class="{ card_small: cardHaveSmallLogo }"
                :style="{ backgroundImage: `url(${cardImage})` }"
            ></div>
        </template>
    </FieldMask>
</template>

<script>
import { IField } from './BaseField';
import FieldMask from './FieldMask';
import BankCard from '../js/BankCard';

export default {
    name: 'FieldCard',
    components: { FieldMask },
    extends: IField('field'),

    data() {
        return {
            cardImage: null,
            cardHaveSmallLogo: false,
        };
    },

    methods: {
        updateCardImage() {
            const type = BankCard.getType(this.getValue());
            this.cardImage = type ? type.image : null;
            this.cardHaveSmallLogo = this.getValue().charAt(0) == 5;
        },

        validate() {
            if (!this.$refs.field.validate(this.getValue())) return false;

            if (this.getValue() && !BankCard.isValid(this.getValue())) {
                this.setError(`Допустимые платежные системы: ${BankCard.getNames()}.`);
                return false;
            }

            return true;
        },
    },
};
</script>

<style scoped lang="less">
    .card {
        background-size: contain;
        background-position: 50%;
        background-repeat: no-repeat;
        position: relative;
        display: inline-block;
        width: 30px;
        line-height: 1em;

        &:before {
            content: '\00a0';
        }

        &_small {
            width: 20px;
        }
    }
</style>
